import React, { useState, FC } from "react";
import GraphVisualizer from "./graph-visualizer";

import {
  Input,
  ActionEdge,
  NodeType,
  GraphType,
  NextAction,
  InputClassNames,
  SourceNodeInput,
} from "src/interfaces";
import { useStore } from "react-stores";
import {
  downloadGlobalStore,
  globalStore,
  INIT_NEXT_ACTION,
  setGlobalStoreFromFile,
  SOURCE_NODE_ID,
} from "../globalStore";
import { compileAndRunMallocSDK } from "src/utils/sdk-interpelator";
import { useAnchorWallet, useWallet } from "@solana/wallet-adapter-react";
import { getSourceNodeAccount } from "src/utils/graph";
import { MallocSPLBuildActionMap } from "@malloc/spl";
import { TableCellEditor } from "./table/table-cells";
import { performUsdcAirdrop } from "./usdc-airdrop/perform-usdc-airdrop";
import { saveStringToFile } from "src/utils/files";
import { Satellite } from "@material-ui/icons";
import { getMintInfo, getTokenAccountInfo } from "src/utils/spl-tokens";
import { getProvider } from "src/utils/anchor";
import { PublicKey } from "@solana/web3.js";
import {
  getActionTypeUIDFromVertex,
  getInitInputForActionType,
} from "src/utils/action-types";

const NodeVisualizer = () => {
  const state = useStore(globalStore);
  const wallet = useAnchorWallet();

  // Control panel loaders
  const [loadingAirdrop, setLoadingAirdrop] = useState(false);
  const [loadingCompileAndRun, setLoadingCompileAndRun] = useState(false);
  // End control panel loaders

  return (
    <div className="card">
      <div className="row">
        <div className="col-6">
          <h2>Visualizer</h2>
        </div>
        <div className="col-2 m-2">
          <button onClick={() => setGlobalStoreFromFile()}>Import</button>
        </div>
        <div className="col-2 m-2">
          <button onClick={() => downloadGlobalStore()}>Export</button>
        </div>
      </div>
      <GraphVisualizer />

      <br />

      <div className="row">
        <div className="col">
          <button
            disabled={loadingCompileAndRun}
            onClick={() => {
              setLoadingCompileAndRun(true);
              compileAndRunMallocSDK(wallet!)
                .then((_) => setLoadingCompileAndRun(false))
                .catch((e) => setLoadingCompileAndRun(false));
            }}
          >
            {loadingCompileAndRun ? "Running..." : "Compile and Run"}
          </button>
        </div>
        <div className="col">
          <button
            onClick={() => {
              setLoadingAirdrop(true);
              performUsdcAirdrop(wallet!.publicKey).then((_) =>
                setLoadingAirdrop(false)
              );
            }}
            disabled={loadingAirdrop}
          >
            {loadingAirdrop ? "Loading Airdrop..." : "Airdrop Fake USDC"}
          </button>
        </div>
        <div className="col-6"></div>
      </div>
    </div>
  );
};

export default NodeVisualizer;
