import { utils } from "@malloc/sdk";
import {
  MintAction,
  MintAndMintToAction,
  SolendAction,
  SwapAction,
  TransferAction,
} from "@malloc/spl";
import { SYSVAR_RENT_PUBKEY } from "@solana/web3.js";
import { getConstants } from "src/config/constants";
import {
  HiddenInputData,
  Input,
  InputClassNames,
  NextAction,
} from "src/interfaces";
import { getVertexType } from "./graph";

export type ActionTypes =
  | "SERUM_SWAP"
  | "TRANSFER"
  | "MINT"
  | "SOLEND DEPOSIT LIQUIDITY";
export const ACTION_TYPES: ActionTypes[] = [
  "SERUM_SWAP",
  "TRANSFER",
  "MINT",
  "SOLEND DEPOSIT LIQUIDITY",
];

export const getActionTypeUIDFromVertex = (vertexType: ActionTypes) => {
  if (vertexType === "SERUM_SWAP") {
    return SwapAction.actionTypeUID;
  } else if (vertexType === "TRANSFER") {
    return TransferAction.actionTypeUID;
  } else if (vertexType === "MINT") {
    return MintAndMintToAction.actionTypeUID;
  } else if (vertexType === "SOLEND DEPOSIT LIQUIDITY") {
    return SolendAction.actionTypeUID;
  } else throw `unexpected vertex id of ${vertexType}`;
};

// TODO: move this over to Malloc SPL and use action type uids instead
export const getActionPIDFromVertex = (vertexType: ActionTypes) => {
  if (vertexType === "SERUM_SWAP") {
    return getConstants().mallocSpl.SWAP_PROGRAM_ID;
  } else if (vertexType === "TRANSFER") {
    return getConstants().mallocSpl.TRANSFER_PROGRAM_ID;
  } else if (vertexType === "MINT") {
    return getConstants().mallocSpl.MINT_TO;
  } else if (vertexType === "SOLEND DEPOSIT LIQUIDITY") {
    return getConstants().mallocSpl.SOLEND_ACTION;
  }
  throw `unexpected vertex id of ${vertexType}`;
};

export const getInitInputForActionType = (
  type: ActionTypes
): { input: Input; hiddenData?: HiddenInputData } => {
  switch (type) {
    case "SOLEND DEPOSIT LIQUIDITY":
      return {
        input: [],
      };
    case "MINT":
      return {
        input: [
          [
            {
              value: "decimals",
              readOnly: true,
            },
            {
              value: "6",
              className: InputClassNames.wholeNumber,
            },
          ],
          [
            {
              value: "initialSupply",
              readOnly: true,
            },
            {
              value: "",
              className: InputClassNames.fractionNumber,
            },
          ],
        ],
      };

    case "TRANSFER":
      return {
        input: [
          [
            {
              value: "recipientWallet",
              readOnly: true,
            },
            { value: "", className: InputClassNames.other },
          ],
        ],
      };

    case "SERUM_SWAP":
      return {
        input: [
          [
            {
              value: "outMint",
              readOnly: true,
            },
            { value: "", className: InputClassNames.mintAccount },
          ],
          [
            {
              value: "minOut",
              readOnly: true,
            },
            { value: "0", className: InputClassNames.fractionNumber },
          ],
        ],
        hiddenData: {
          rent: SYSVAR_RENT_PUBKEY.toBase58(),
          serumDexProgramId: getConstants().projectSerum.DEX_PROGRAM_ID,
        },
      };

    default:
      throw `Unexpected action type ${type}`;
  }
};

export const setHardCodedNextActions = (
  nextActions: NextAction[]
): NextAction[] => {
  const data = utils.object.deepCloneObject(nextActions);
  for (let x = 0; x < data.length; x++) {
    // TODO: this shouldn't be hardcoded
    if (getVertexType(data[x][0]!.value) === "MINT") {
      data[x][1]!.value = "0";
      data[x][1]!.readOnly = true;
    }
  }
  return data;
};
