import { Wallet } from "@project-serum/anchor";
import { createTokenAccount, Provider } from "@project-serum/common";
import {
  ASSOCIATED_TOKEN_PROGRAM_ID,
  Token,
  TOKEN_PROGRAM_ID,
} from "@solana/spl-token";
import { Connection, Keypair, PublicKey, Transaction } from "@solana/web3.js";
import { getConstants } from "../../config/constants";

const richUSDCAirdroperSK = new Uint8Array([
  8, 57, 204, 222, 105, 129, 113, 8, 245, 54, 20, 45, 122, 175, 82, 37, 32, 44,
  46, 6, 54, 35, 168, 208, 213, 194, 164, 50, 97, 226, 134, 119, 136, 196, 213,
  189, 243, 233, 16, 53, 22, 220, 245, 4, 17, 203, 251, 233, 20, 7, 178, 5, 36,
  19, 66, 89, 210, 148, 19, 225, 117, 61, 214, 253,
]);

const richUSDCSigner = new Wallet(Keypair.fromSecretKey(richUSDCAirdroperSK));

const getRichUsdcProvider = (endpointURL: string) =>
  new Provider(new Connection(endpointURL), richUSDCSigner, {
    commitment: "confirmed",
  });

export const performUsdcAirdrop = async (userAddr: PublicKey) => {
  try {
    const tokAccount = await tryCreateAssociatedAccount(
      new PublicKey(getConstants().demo.FAKE_USDC),
      userAddr,
      getRichUsdcProvider(getConstants().solana.RPC)
    );
    await airdrop(new PublicKey(getConstants().demo.FAKE_USDC_GD), [
      tokAccount,
    ]);
    alert("Done with your airdrop");
  } catch (e) {
    console.error(e);
    alert(`An error occurred with the airdrop ${JSON.stringify(e)}`);
  }
};

const tryCreateAssociatedAccount = async (
  mint: PublicKey,
  account: PublicKey,
  provider: Provider
): Promise<PublicKey> => {
  const associated = (
    await PublicKey.findProgramAddress(
      [
        account.toBuffer(),
        TOKEN_PROGRAM_ID.toBuffer(),
        new PublicKey(mint).toBuffer(),
      ],
      ASSOCIATED_TOKEN_PROGRAM_ID
    )
  )[0];
  const data = await provider.connection.getAccountInfo(associated);
  if (!data) {
    const instr = Token.createAssociatedTokenAccountInstruction(
      ASSOCIATED_TOKEN_PROGRAM_ID,
      TOKEN_PROGRAM_ID,
      mint,
      associated,
      account,
      provider.wallet.publicKey
    );
    const tx = new Transaction();
    tx.add(instr);
    await provider.send(tx, []);
    return associated;
  }
  return associated;
};

const airdrop = async (
  gdUsdc: PublicKey,
  airDropToTokenAccounts: PublicKey[]
) => {
  const decimals = 6;
  const amountAirdrop = 4 * 10 ** decimals;

  const transferTxs: Transaction = new Transaction();
  airDropToTokenAccounts.map((tokAccount) => {
    transferTxs.add(
      Token.createTransferInstruction(
        TOKEN_PROGRAM_ID,
        gdUsdc,
        tokAccount,
        richUSDCSigner.publicKey,
        [],
        amountAirdrop
      )
    );
  });

  await getRichUsdcProvider(getConstants().solana.RPC).send(transferTxs, [], {
    skipPreflight: true,
  });
};
