import { ActionMetadata } from "./interfaces";

// TODO: there is a lot of cool stuff to do here

type ToHitArg = { called: number[] };

export const buildSeqListOfActionCalls = (
  actionMetas: ActionMetadata[],
  initIndices: number[]
): number[] => {
  const initHitCounts = [...Array(actionMetas.length)].fill(0);
  const called = simulateDAG(
    actionMetas.map((a, i) => {
      return { ...a, id: i };
    }),
    initHitCounts,
    initIndices
  );

  return called;
};

// Returns an ordered list of indices hit
const simulateDAG = (
  actionMetas: (ActionMetadata & { id: number })[],
  hitCounts: number[],
  toHit: number[]
): number[] => {
  if (toHit.length === 0) return [];
  const newHitCounts = hitCounts.map((e, i) => getOccurrence(toHit, i) + e)
  const newRipeActions = actionMetas.filter(
    (a) => a.ripeAmount === newHitCounts[a.id] && toHit.includes(a.id)
  );
  const newToHit = newRipeActions
    .map((a) => a.nextNodes.map((o) => o.map((i) => i.actionIdx)))
    .flat()
    .flat();
  return [
    ...newRipeActions.map((a) => a.id),
    ...simulateDAG(actionMetas, newHitCounts, newToHit),
  ];
};

const getOccurrence = <T>(array: T[], value: T) =>
  array.filter((v) => v === value).length;
