import { ActionLib, BuildActionMap } from "@malloc/sdk";
import MintAction from "./mint";
import { MintAndMintToAction } from "./mint-to";
import { SolendAction } from "./solend";
import SwapAction from "./swap";
import { TransferAction } from "./transfer";

export { SwapAction } from "./swap";
export { TransferAction } from "./transfer";
export { MintAction } from "./mint";
export { MintAndMintToAction } from "./mint-to";
export { SolendAction } from "./solend";

export const MallocSPLBuildActionMap: BuildActionMap = {};

// Add the spl actions to the default mapping
const addActionToMap = (actionLib: ActionLib<any>) => {
  MallocSPLBuildActionMap[actionLib.actionTypeUID] = actionLib;
};

addActionToMap(SwapAction);
addActionToMap(TransferAction);
addActionToMap(MintAction);
addActionToMap(MintAndMintToAction);
addActionToMap(SolendAction);
