import { getProvider, Provider } from "@project-serum/anchor";
import { parseTokenAccount, parseMintAccount } from "@project-serum/common";
import {
  AccountInfo,
  ASSOCIATED_TOKEN_PROGRAM_ID,
  MintInfo,
  TOKEN_PROGRAM_ID,
  u64,
} from "@solana/spl-token";
import {
  TokenListProvider,
  TokenInfo,
  TokenListContainer,
} from "@solana/spl-token-registry";
import { PublicKey } from "@solana/web3.js";
import { getConstants } from "../config/constants";
import { solanaSplStore } from "../state/flowchart-store";

// TODO: where does this come from?
const SPL_ASSOCIATED_TOKEN_ACCOUNT_PROGRAM_ID: PublicKey =
  ASSOCIATED_TOKEN_PROGRAM_ID;

// TODO: have a store for token metadata which has a list of all tokens as well as their metadata

// See https://github.com/solana-labs/token-list
export const getTokenLists = async (
  provider: Provider
): Promise<TokenInfo[]> => {
  const getCached = async () => {
    if (solanaSplStore.state.tokenList) return solanaSplStore.state.tokenList;
    const tokens = await new TokenListProvider().resolve();
    const tokenList = tokens
      .filterByClusterSlug(getConstants().solana.NETWORK)
      .getList();
    solanaSplStore.setState({
      ...solanaSplStore.state,
      tokenList,
    });
    return tokenList;
  };
  const tokenList = await getCached();

  // Add in demo tokens
  if (getConstants().solana.NETWORK === "devnet") {
    return [...tokenList, ...(await getDemoTokenInfo(provider))];
  } else {
    return tokenList;
  }
};

const getDemoTokenInfo = async (provider: Provider): Promise<TokenInfo[]> => {
  const usdcMint = new PublicKey(getConstants().demo.FAKE_USDC);
  const aMint = new PublicKey(getConstants().demo.FAKE_TOKEN_A);
  const usdcInfo = await getMintInfo(provider, usdcMint);
  const aInfo = await getMintInfo(provider, aMint);
  if (!usdcInfo || !aInfo) {
    throw "Expected info for these mints";
  }
  return [
    {
      ...usdcInfo,
      chainId: -1,
      name: "FAKE USDC",
      symbol: "FUSDC",
      address: getConstants().demo.FAKE_USDC,
    },
    {
      ...aInfo,
      chainId: -1,
      name: "FAKE: A TOKEN",
      symbol: "FA TOKEN",
      address: getConstants().demo.FAKE_TOKEN_A,
    },
  ];
};

export const getTokenListContainer = async (
  provider: Provider
): Promise<TokenListContainer> =>
  new TokenListContainer(await getTokenLists(provider));

// See https://spl.solana.com/associated-token-account
export const findAssociatedTokenAddress = async (
  walletAddress: PublicKey,
  tokenMintAddress: PublicKey | string
): Promise<PublicKey> => {
  const associated = await PublicKey.findProgramAddress(
    [
      walletAddress.toBuffer(),
      TOKEN_PROGRAM_ID.toBuffer(),
      new PublicKey(tokenMintAddress).toBuffer(),
    ],
    SPL_ASSOCIATED_TOKEN_ACCOUNT_PROGRAM_ID
  );
  return associated[0];
};
export const findAllAssociatedTokenAddress = async (
  walletAddress: PublicKey,
  mints: (PublicKey | string)[]
): Promise<PublicKey[]> =>
  Promise.all(mints.map((m) => findAssociatedTokenAddress(walletAddress, m)));

export const getTokenAccountInfo = async (
  provider: Provider,
  tokenAddr: PublicKey
): Promise<AccountInfo | null> => {
  const account = await provider.connection.getAccountInfo(tokenAddr);
  if (!account) return null;
  try {
    const data = parseTokenAccount(account.data);
    return data;
  } catch (e) {
    console.error("An error occured parsing the data, returning null", e);
    return null;
  }
};

export const getMintInfo = async (
  provider: Provider,
  mintAddr: PublicKey
): Promise<MintInfo | null> => {
  const account = await provider.connection.getAccountInfo(mintAddr);
  if (!account) return null;
  return parseMintAccount(account.data);
};
