import React, { Ref, useRef } from "react";
import { makeStyles, Theme, createStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import TextField from "@material-ui/core/TextField";
import { useMemo } from "react";
import { useEffect } from "react";
import {
  findAllAssociatedTokenAddress,
  findAssociatedTokenAddress,
  getMintInfo,
  getTokenAccountInfo,
  getTokenLists,
} from "../../utils/spl-tokens";
import { useAnchorWallet } from "@solana/wallet-adapter-react";
import { TokenInfo } from "@solana/spl-token-registry";
import { SPLToken } from "../../interfaces";
import { shortenAddress } from "../../utils/malloc";
import { Provider } from "@project-serum/anchor";
import { getProvider } from "../../utils/anchor";
import { isBundle } from "typescript";
import { useState } from "react";
import { PublicKey } from "@solana/web3.js";
import { useStore } from "react-stores";
import { solanaSplStore } from "../../state/flowchart-store";
import { u64 } from "@solana/spl-token";
import { utils } from "@malloc/sdk";

function getModalStyle() {
  const top = 50;
  const left = 50;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    paper: {
      position: "absolute",
      width: "auto",
      backgroundColor: theme.palette.background.paper,
      border: "2px solid #000",
      boxShadow: theme.shadows[5],
      padding: theme.spacing(2, 4, 3),
    },
  })
);

const TokenLine = ({
  tok,
  setChoosen,
}: {
  tok: SPLToken;
  setChoosen: () => void;
}) => {
  return (
    <div
      style={{
        display: "grid",
        gridTemplateColumns: "1fr 1fr 1fr 1fr 1fr",
        width: "100%",
        gap: "5px",
      }}
    >
      <button onClick={setChoosen}>Choose</button>
      <div>{tok.info.name}</div>
      <div>{shortenAddress(tok.info.mint.toBase58())}</div>
      <div>
        {shortenAddress(tok.account)}
        {tok.balance ? "" : " (currently nonexistent)"}
      </div>
      <div>
        {utils.tokens.toReadableNumber(
          new u64(tok.balance || 0),
          tok.info.decimals
        )}
      </div>
    </div>
  );
};

export default function TokenAccountChooser(props: {
  buttonText?: string;
  onlyWithInitializedAccounts?: boolean;
  onClose: (tok?: SPLToken) => void;
  includeCustomTokenAccount?: boolean;
}) {
  const onlyWithInitializedAccounts = props.onlyWithInitializedAccounts ?? true;
  const classes = useStyles();
  // getModalStyle is not a pure function, we roll the style only on the first render
  const [modalStyle] = React.useState(getModalStyle);
  const [open, setOpen] = React.useState(false);
  const [customTokenAccount, setCustomTokenAccount] = React.useState("");
  const [customMintAccount, setCustomMintAccount] = React.useState("");
  const wallet = useAnchorWallet();
  // If it is -1, then the custom state is choosen
  const [choosen, setChoosen] = useState(-1);
  const solanaSplStoreHook = useStore(solanaSplStore);

  const provider = getProvider(wallet!);

  const loadTokens = async () => {
    const tokenList = await getTokenLists(provider);
    const associatedAddr = await findAllAssociatedTokenAddress(
      wallet?.publicKey!,
      tokenList.map((t) => t.address)
    );

    const bals = await Promise.all(
      associatedAddr.map(async (addr) => {
        const data = await getTokenAccountInfo(provider, addr);
        return data?.amount;
      })
    );
    const accounts = associatedAddr.map((addr, i) => {
      return {
        account: addr.toBase58(),
        info: tokenList[i],
        balance: bals[i],
      };
    });
    const accountsFiltered = onlyWithInitializedAccounts
      ? accounts.filter((i) => i.balance)
      : accounts;
    const accountsMapped = accountsFiltered.map((i) => {
      return {
        ...i,
        info: {
          ...i.info,
          mint: new PublicKey(i.info.address),
        },
        balance: i.balance?.toString() || null,
      };
    });

    solanaSplStore.setState({
      ...solanaSplStoreHook,
      derivedTokenAccounts: accountsMapped,
    });
  };

  useEffect(() => {
    loadTokens();
  }, []);

  const modalRef = useRef(null);

  const handleOpen = () => {
    loadTokens();
    setOpen(true);
  };

  const handleClose = async () => {
    if (!solanaSplStoreHook.derivedTokenAccounts) {
      setOpen(false);
      props.onClose();
      return;
    }
    if (choosen >= 0)
      props.onClose(solanaSplStoreHook.derivedTokenAccounts[choosen]);
    else {
      const choosenType = choosen === -1 ? "TOKEN" : "MINT";
      if (
        (choosenType === "TOKEN" && !customTokenAccount) ||
        (choosenType === "MINT" && !customMintAccount)
      ) {
        setOpen(false);
        props.onClose();
        return;
      }

      const tokenAccount =
        choosenType === "TOKEN"
          ? customTokenAccount
          : (
              await findAssociatedTokenAddress(
                wallet!.publicKey,
                customMintAccount
              )
            ).toBase58();
      const tokAccountData = await getTokenAccountInfo(
        provider,
        new PublicKey(tokenAccount)
      );
      if (choosenType === "TOKEN" && !tokAccountData) {
        alert("Looks like an invalid SPL Token Account was provided");
        setOpen(false);
        props.onClose();
        return;
      }
      if (
        choosenType === "MINT" &&
        props.onlyWithInitializedAccounts !== false &&
        !tokAccountData
      ) {
        alert(
          "Looks like there is no initialized associated account with this mint"
        );
        setOpen(false);
        props.onClose();
        return;
      }
      const mintAddr =
        choosenType === "TOKEN"
          ? tokAccountData!.mint
          : new PublicKey(customMintAccount);

      const mintInfo = await getMintInfo(provider, mintAddr);
      if (!mintInfo) {
        alert("Looks like the mint associated with this token is invalid");
        setOpen(false);
        props.onClose();
        return;
      }
      const bal = tokAccountData?.amount || new u64(0);
      props.onClose({
        balance: bal,
        account: tokenAccount,
        info: {
          ...mintInfo,
          mint: mintAddr,
        },
      });
    }
    setOpen(false);
    props.onClose();
  };

  const body = (
    <div
      onBlur={handleClose}
      className="wrapper"
      // TODO: this is a bit ghetto but react modal isn't working with the cells
      onClick={(e) => e.currentTarget.blur()}
      style={{
        position: "fixed",
        top: "0",
        left: "0",
        height: "100vh",
        width: "100vw",
        zIndex: 100,
        display: "grid",
        justifyItems: "center",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <div style={{ ...modalStyle }} className={classes.paper}>
        <h2 id="simple-modal-title">Choose a token account</h2>
        <p id="simple-modal-description">
          Choose from either your associated token accounts or add a custom
          address
        </p>
        <div
          className="options"
          style={{ overflow: "scroll", maxHeight: "70vh" }}
        >
          <div
            style={{
              display: "grid",
              gridTemplateColumns: "1fr 1fr 1fr 1fr 1fr",
            }}
          >
            <span></span>
            <div>Mint Name</div>
            <div>Mint Address</div>
            <div>Token Account Address</div>
            <div>Amount</div>
          </div>
          {!solanaSplStoreHook.derivedTokenAccounts
            ? "Loading..."
            : solanaSplStoreHook.derivedTokenAccounts.map((tok, i) => (
                <div
                  key={`token-derived-account-${i}`}
                  style={{
                    padding: "0.5rem",
                    border: choosen === i ? "1px solid black" : "0px",
                  }}
                >
                  <TokenLine
                    tok={tok}
                    setChoosen={() => {
                      setChoosen(i);
                    }}
                  />
                  <br />
                </div>
              ))}
          {/* TODO: add balance to the left of this once filled out */}
          {props.includeCustomTokenAccount && (
            <div
              style={{
                padding: "0.5rem",
                border: choosen === -1 ? "1px solid black" : "0px",
              }}
            >
              <TextField
                label="Custom Token Account"
                value={customTokenAccount}
                onChange={(e) => setCustomTokenAccount(e.target.value)}
                onFocus={() => setChoosen(-1)}
              />
            </div>
          )}
          <p>Or</p>
          <div
            style={{
              padding: "0.5rem",
              border: choosen === -2 ? "1px solid black" : "0px",
            }}
          >
            <TextField
              label="Custom Mint Account"
              value={customMintAccount}
              onChange={(e) => setCustomMintAccount(e.target.value)}
              onFocus={() => setChoosen(-2)}
            />
          </div>
        </div>
      </div>
    </div>
  );

  return (
    <div>
      <button type="button" onClick={handleOpen}>
        {props.buttonText || "Choose Token Account"}
      </button>

      {/* <Modal
        open={open}
        ref={modalRef}
        onClose={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      > */}
      {open && body}
      {/* </Modal> */}
    </div>
  );
}
