import { Store } from "react-stores";
import {
  Input,
  ActionEdge,
  NodeType,
  GraphType,
  SourceNodeInput,
  NextAction,
  HiddenInputData,
  InputClassNames as CellClassNames,
} from "src/interfaces";
import { getFileContent, saveStringToFile } from "./utils/files";
import { getNextActionRow } from "./utils/graph";

export interface MallocState {
  selected: string;
  counter: number;
  new_action: string;
  graph: GraphType;
  sourceMint: string;
  hiddenInputData: HiddenInputData;
}

export const SOURCE_NODE_ID = "SOURCE-0";

export const INIT_NEXT_ACTION: NextAction[] = [
  [
    { value: "NextActionID", readOnly: true },
    { value: "Parts of 1,000", readOnly: true },
  ],
  getNextActionRow(0),
];

const initState: MallocState = {
  selected: SOURCE_NODE_ID,
  counter: 1,
  new_action: "SERUM_SWAP",
  graph: {},
  sourceMint: "<UNSET>",
  hiddenInputData: {},
};

initState.graph[SOURCE_NODE_ID] = {
  input: [
    [
      {
        value: "InputTokenAccount",
        readOnly: true,
      },
      { value: "", className: CellClassNames.tokenAccountOnlyInit },
    ],
    [
      {
        value: "Amount",
        readOnly: true,
      },
      { value: "", className: CellClassNames.fractionNumber },
    ],
  ] as SourceNodeInput,
  next_actions: [INIT_NEXT_ACTION],
};

export const globalStore = new Store<MallocState>(initState);

export const getGlobalStore = () => globalStore.state;

export const setGlobalStoreFromFile = async () => {
  const fileContents = await getFileContent();
  if (fileContents.length < 1) {
    alert("Please select a file");
    return;
  }
  // TODO: use have the input be the nonspecific construction (and validated via malloc sdk) and then have a function which interpolates
  const parsed = JSON.parse(await fileContents[0].text()) as MallocState;
  console.log(parsed);
  // Set the token account in to be undefined
  parsed.graph[SOURCE_NODE_ID].input[0][1].value = "";
  globalStore.setState(parsed);
};

export const downloadGlobalStore = async () => {
  await saveStringToFile(JSON.stringify(globalStore.state), "flow.json");
};
