import { ActionLib, BNIsh, BuildIsolatedActionFn, utils } from "@malloc/sdk";
import IDL from "../../target/idl/mint_to_action.json";
import { serialize as borshSer } from "borsh";
import { TOKEN_PROGRAM_ID, u64 } from "@solana/spl-token";
import {
  Account,
  Ed25519Program,
  Keypair,
  PublicKey,
  Signer,
  Transaction,
} from "@solana/web3.js";
import { Provider } from "../../../../anchor/ts/src";
import { SendTxRequest } from "../../../../anchor/ts/src/provider";
import { createMintInstructions } from "@project-serum/common";
import { Idl } from "@project-serum/anchor";

// TODO: optionality to not just create a new mint
/**
 * @param authoritySecret - a hex encoded secret key
 * @param createMint.amount - the amount to mint to, this will be packed into the data and will overwrite amount_in. This is in human readable format
 */
type BuildMintToArgs = {
  authoritySecret?: string;
  // createMint?: {
  // TODO: add back in the above once the frontend can give in a struct
  decimals: number;
  createMintSecretKey?: string; // The secret key of the mint in hex
  initialSupply: string;
  // };
};

const actionTypeUID = "MALLOC_SPL_MINT_TO";

class ActionData extends utils.serde.Assignable {}

const schema = new Map([
  [
    ActionData,
    {
      kind: "struct",
      fields: [["amount_override", { kind: "option", type: "u64" }]],
    },
  ],
]);

export const _mintToSerializeData = (amount?: BNIsh): Buffer => {
  return Buffer.from(
    borshSer(schema, new ActionData({ amount_override: new u64(amount) }))
  );
};

const buildIsolatedMintTo: BuildIsolatedActionFn<BuildMintToArgs> = async (
  inp: BuildMintToArgs,
  inputMint: string,
  inputTokenAccount: string,
  mintToPID,
  provider: Provider
) => {
  // TODO: note the below is untested and will remain so till the hackathon is over
  const authoritySigner = inp.authoritySecret
    ? Keypair.fromSecretKey(
        new Uint8Array(Buffer.from(inp.authoritySecret, "hex"))
      )
    : undefined;

  // If the user is creating a new mint, then use the new mint address, otherwise use the input mint
  const mint = inp.createMintSecretKey
    ? Keypair.fromSecretKey(
        new Uint8Array(Buffer.from(inp.createMintSecretKey, "hex"))
      )
    : new Keypair();

  // If their is not specified recipient, mint to the caller
  const associateTokAccountRecipient =
    await utils.tokens.getTokenAccountUsedByMalloc(
      provider.wallet.publicKey,
      mint.publicKey
    );

  const optionalAmountWholeNumber = utils.tokens.fromReadableNumber(
    parseFloat(inp.initialSupply),
    inp.decimals
  );

  const atStartTxs: SendTxRequest[] = [];

  const authority = new PublicKey(provider.wallet.publicKey);
  const tx = new Transaction();
  tx.add(
    ...(await createMintInstructions(
      provider,
      authority,
      mint.publicKey,
      inp.decimals
    ))
  );
  atStartTxs.push({
    tx,
    signers: [mint],
  });

  return {
    isolatedAction: {
      tokenMintIn: new PublicKey(inputMint),
      actionData: _mintToSerializeData(optionalAmountWholeNumber),
      actionProgram: new PublicKey(mintToPID),
      actionAccounts: [
        {
          address: mint.publicKey,
          isWriteable: true,
        },
        {
          address: associateTokAccountRecipient,
          isWriteable: true,
        },
        {
          address: authoritySigner?.publicKey || provider.wallet.publicKey,
          isSigner: true,
        },
        {
          address: TOKEN_PROGRAM_ID,
        },
      ],
      additionalSigners: authoritySigner ? [authoritySigner] : [],
      tokenMintOuts: [mint.publicKey],
      tokenAccountOuts: [associateTokAccountRecipient],
      atStartOfPrepareTxs: atStartTxs,
      // TODO: once JIT token creation is added, move creating the mint to an instruction and change the token creation to
      // be right here!
      // tokenCreationOptions: {
      //   skipInTokenCreation: true,
      // },
    },
  };
};

export const MintAndMintToAction: ActionLib<BuildMintToArgs> = {
  buildIsolatedAction: buildIsolatedMintTo,
  actionTypeUID,
  expectedNumberOfOutMints: 1,
  idl: IDL as Idl,
};
