import { SYSVAR_RENT_PUBKEY } from "@solana/web3.js";
import { getConstants } from "src/config/constants";
import { INIT_NEXT_ACTION, SOURCE_NODE_ID } from "src/globalStore";
import {
  ActionEdge,
  GraphType,
  HiddenInputData,
  Input,
  InputClassNames,
  NextAction,
  SourceNodeInput,
} from "src/interfaces";
import { ActionTypes } from "./action-types";

export const getSourceNode = (g: GraphType) => g[SOURCE_NODE_ID];

export const getSourceNodeAccount = (inp: SourceNodeInput) => inp[0][1].value;
export const getSourceNodeAmount = (inp: SourceNodeInput) => inp[1][1].value;
export const getAllVertices = (g: GraphType): string[] => Object.keys(g);
export const getActionVertices = (g: GraphType): string[] => {
  const all = Object.keys(g);
  const initIdx = all.indexOf(SOURCE_NODE_ID);
  all.splice(initIdx, 1);
  return all;
};

export const getVertexType = (v: string): ActionTypes =>
  v.split("-")[0] as ActionTypes;

// Discard the first element as those are just labels
export const getNextVertices = (
  g: GraphType,
  vertex: string
): ActionEdge[][] => {
  const nextActions = g[vertex].next_actions;
  const ret = nextActions.map((nextActionSet) => {
    const nextActionRemoveLabel = nextActionSet.slice(1);
    const edgesRet = nextActionRemoveLabel.map((nextAction: NextAction) => {
      if (!nextAction[1].value && !nextAction[0].value) {
        return null;
      }
      return {
        amount: parseInt(nextAction[1].value),
        id: nextAction[0].value,
      } as ActionEdge;
    });
    // Remove empty rows
    return edgesRet.filter((e, i) => e !== null) as ActionEdge[];
  });
  return ret;
};

export const getNextActionRow = (nextMintIdx: number) => {
  return [
    { value: "", className: `${InputClassNames.actionVertex}-${nextMintIdx}` },
    { value: "", className: InputClassNames.wholeNumber },
  ] as NextAction;
};
