export const CONSTRUCTION_ACCOUNT_SIZE = 2e4; // Around 3 kilobytes
const ONE_HOUR_MS = 1000 * 60 * 60;
const HOURS_PER_EPOCH = 48;
export const LIVE_N_EPOCHS = 3;
export const LIFETIME_RESULT_ACCOUNT_MS = ONE_HOUR_MS * (HOURS_PER_EPOCH * LIVE_N_EPOCHS); // Has to be more than 1 epoch, lets be sage
export const LIFETIME_CONSTRUCTION_ACCOUNT_MS =
  ONE_HOUR_MS * (HOURS_PER_EPOCH * LIVE_N_EPOCHS);

// Rent constants from https://docs.solana.com/developing/programming-model/accounts
// TODO: should this be from a dynamic value?
export const LAMPORTS_PER_EPOCH = 19.055;
export const MS_PER_EPOCH = ONE_HOUR_MS * HOURS_PER_EPOCH;
