// TODO: there is probably a faster way to do this
export const deepCloneObject = (obj: any): any => {
  function clone(obj) {
    if (obj == null || typeof obj != "object") return obj;

    var temp = obj.constructor();

    for (var key in obj) temp[key] = clone(obj[key]);
    return temp;
  }
  return clone(obj);
};

export const findAllIndexes = <T>(arr: T[], discriminator: (T) => boolean): number[] => {
  var indexes = []
  for (let i = 0; i < arr.length; i++) {
    if (discriminator(arr[i])) indexes.push(i);
  }
  return indexes;
};
