import { BN, Instruction } from "@project-serum/anchor";
import { Provider, Wallet } from "../../../../../../anchor/ts";
import { transfer } from "@project-serum/serum/lib/token-instructions";
import {
  ASSOCIATED_TOKEN_PROGRAM_ID,
  Token,
  TOKEN_PROGRAM_ID,
  u64,
} from "@solana/spl-token";
import {
  Account,
  Connection,
  PublicKey,
  Signer,
  Transaction,
  TransactionInstruction,
} from "@solana/web3.js";
import { Action } from "../interfaces";
import { createTokenAccount, SendTxRequest } from "@project-serum/common";

export const tryCreateAssociatedAccountInst = async (
  mint: PublicKey,
  owner: PublicKey,
  connection: Connection,
  payer: PublicKey
): Promise<{
  associateTokAccount: PublicKey;
  instr?: TransactionInstruction;
}> => {
  const associated = (
    await PublicKey.findProgramAddress(
      [
        owner.toBuffer(),
        TOKEN_PROGRAM_ID.toBuffer(),
        new PublicKey(mint).toBuffer(),
      ],
      ASSOCIATED_TOKEN_PROGRAM_ID
    )
  )[0];
  const data = await connection.getAccountInfo(associated);
  if (!data) {
    const inst = Token.createAssociatedTokenAccountInstruction(
      ASSOCIATED_TOKEN_PROGRAM_ID,
      TOKEN_PROGRAM_ID,
      new PublicKey(mint),
      associated,
      owner,
      payer
    );
    return {
      instr: inst,
      associateTokAccount: associated,
    };
  } else {
    return {
      associateTokAccount: associated,
    };
  }
};

export const tryCreateAssociatedAccount = async (
  mint: PublicKey,
  owner: PublicKey,
  provider: Provider
) => {
  const { associateTokAccount, instr } = await tryCreateAssociatedAccountInst(
    mint,
    owner,
    provider.connection,
    provider.wallet.publicKey
  );
  if (!instr) return associateTokAccount;
  const tx = new Transaction();
  tx.add(instr);
  await provider.send(tx);
  return associateTokAccount;
};

export interface AccountByMint {
  [mint: string]: { publicKey: PublicKey };
}

export const transferToTx = async (
  source: PublicKey,
  destination: PublicKey,
  amount: BN,
  authority: Signer | Wallet
): Promise<SendTxRequest> => {
  const signer = (authority as Signer).secretKey ? [authority as Account] : [];
  const inst = transfer({
    source,
    destination,
    amount,
    owner: authority.publicKey,
  });
  const tx = new Transaction();
  tx.add(inst);
  return { tx, signers: signer };
};

export type PreferredTokenAccounts = { [mint: string]: PublicKey };

/**
 * Get the token account which the malloc sdk will use for a mint
 *
 * @param preferredTokenAccounts - a map of mints to token accounts which the caller would prefer to use per mint. If the account does not exist,
 * and is needed it will be created in the the returned txs
 */
export const getTokenAccountUsedByMalloc = async (
  tokenAuthority: PublicKey,
  mintAccount: PublicKey,
  preferredTokenAccounts?: PreferredTokenAccounts
): Promise<PublicKey> => {
  if (
    preferredTokenAccounts &&
    preferredTokenAccounts[mintAccount.toBase58()]
  ) {
    return preferredTokenAccounts[mintAccount.toBase58()];
  }
  return await findAssociatedTokenAddress(tokenAuthority, mintAccount);
};

/**
 * Takes in a mapping of mints to token accounts. If a token account has yet to exist, one will be made
 * which is associated to the authority's address
 *
 * @param preferredTokenAccounts - a map of mints to token accounts which the caller would prefer to use per mint. If the account does not exist,
 * and is needed it will be created in the the returned txs
 */
export const createTokenAccountsForActions = async (
  provider: Provider,
  tokenAuthority: Signer | Wallet,
  actions: Action[],
  fundingAccount: Signer | Wallet,
  preferredTokenAccounts?: PreferredTokenAccounts
): Promise<{ accounts: AccountByMint; txs: SendTxRequest[] }> => {
  const mints = getMintsNoDuplicates(
    actions
      .map((a) => {
        const mints = [];
        if (a.tokenCreationOptions?.skipOutTokenCreations || false) {
          const skipIdxs = a.tokenCreationOptions.skipOutTokenCreations;
          console.log(skipIdxs);
          // Skip all the mints which are included by the skipIdxs
          a.tokenMintOuts.forEach((mint, i) => {
            if (!skipIdxs.includes(i)) mints.push(mint);
          });
        } else {
          mints.push(...a.tokenMintOuts);
        }
        if (a.tokenCreationOptions?.skipInTokenCreation === true) {
        } else {
          mints.push(a.tokenMintIn);
        }
        return mints;
      })
      .flat()
  );
  console.log(mints);

  let accounts: AccountByMint = {};
  for (let i = 0; i < mints.length; i++) {
    const mint = mints[i];
    if ((preferredTokenAccounts || {})[mint])
      accounts[mint] = { publicKey: preferredTokenAccounts[mint] };
    // create the token account if not provided
    else {
      const pk = await findAssociatedTokenAddress(
        tokenAuthority.publicKey,
        mint
      );
      accounts[mint] = {
        publicKey: pk, // TODO: check that this account does not yet exist
      };
    }
  }

  const createTokTx = await Promise.all(
    mints.map(async (mint) => {
      const associatedToSignerAccount = accounts[mint].publicKey;
      const accountExists = await provider.connection.getAccountInfo(
        associatedToSignerAccount
      );

      // Return if the account already exists
      if (accountExists) {
        console.log("ALREADY EXISTS");
        return null;
      }
      const insts = Token.createAssociatedTokenAccountInstruction(
        ASSOCIATED_TOKEN_PROGRAM_ID,
        TOKEN_PROGRAM_ID,
        new PublicKey(mint),
        accounts[mint].publicKey,
        tokenAuthority.publicKey,
        fundingAccount.publicKey
      );
      const tx = new Transaction();
      tx.add(insts);
      const signers: Account[] = (fundingAccount as Signer).secretKey
        ? [fundingAccount as Account]
        : [];
      return {
        tx,
        signers: signers,
      };
    })
  );

  return { accounts: accounts, txs: createTokTx.filter((i) => !!i) };
};

// TODO: if there are 2 token accounts of the same type, one of them not being made, then there will be an error out

// See https://spl.solana.com/associated-token-account
const findAssociatedTokenAddress = async (
  walletAddress: PublicKey,
  tokenMintAddress: PublicKey | string
): Promise<PublicKey> => {
  return (
    await PublicKey.findProgramAddress(
      [
        walletAddress.toBuffer(),
        TOKEN_PROGRAM_ID.toBuffer(),
        new PublicKey(tokenMintAddress).toBuffer(),
      ],
      ASSOCIATED_TOKEN_PROGRAM_ID
    )
  )[0];
};

const getMintsNoDuplicates = (mints: PublicKey[]) => [
  ...new Set(mints.map(pkToStr)),
];

const pkToStr = (pk: PublicKey) => pk.toBase58();
const pkFromStr = (pk: string) => new PublicKey(pk);

export const toReadableNumber = (number: u64, decimals: number): string => {
  function trimTrailingZeroes(value: string): string {
    return value.replace(/\.?0*$/, "");
  }
  function formatWithCommas(value: string): string {
    const pattern = /(-?\d+)(\d{3})/;
    while (pattern.test(value)) {
      value = value.replace(pattern, "$1,$2");
    }
    return value;
  }

  const balance = number.toString();
  const wholeStr = balance.substring(0, balance.length - decimals) || "0";
  // TODO: correct?
  const fractionStr = balance
    .substring(balance.length - decimals)
    .padStart(decimals, "0")
    .substring(0, decimals);

  return trimTrailingZeroes(`${formatWithCommas(wholeStr)}.${fractionStr}`);
};

export const fromReadableNumber = (
  number: number,
  decimals: number
): string => {
  function trimLeadingZeroes(value: string): string {
    value = value.replace(/^0+/, "");
    if (value === "") {
      return "0";
    }
    return value;
  }

  const split = number.toString().split(".");
  const wholePart = split[0];
  const fracPart = split[1] || "";
  if (split.length > 2 || fracPart.length > decimals) {
    throw new Error(`Cannot parse '${number}' as token amount`);
  }
  return trimLeadingZeroes(wholePart + fracPart.padEnd(decimals, "0"));
};
