import { ActionLib, BuildIsolatedActionFn, utils } from "@malloc/sdk";
import { PublicKey } from "@solana/web3.js";
import { Idl, Provider } from "../../../../anchor/ts/src";
import IDL from "../../target/idl/transfer_action.json";

type BuildTransferArgs = {
  recipientWallet: string;
};

const actionTypeUID = "MALLOC_SPL_TRANSFER";

const buildIsolatedTransfer: BuildIsolatedActionFn<BuildTransferArgs> = async (
  inp: BuildTransferArgs,
  mintAccount: string,
  inputTokenAccount: string,
  transferPID,
  provider: Provider
) => {
  const mintIn = new PublicKey(mintAccount);
  const recipient = new PublicKey(inp.recipientWallet);
  const { associateTokAccount, instr: createTokInstr } =
    await utils.tokens.tryCreateAssociatedAccountInst(
      mintIn,
      recipient,
      provider.connection,
      provider.wallet.publicKey
    );

  return {
    isolatedAction: {
      actionTypeUID,
      addDefaultTokAccounts: true,
      tokenMintIn: mintIn,
      actionData: Buffer.from([]),
      actionProgram: new PublicKey(transferPID),
      actionAccounts: [
        {
          address: associateTokAccount,
          isWriteable: true,
        },
      ],
      additionalSigners: [],
      tokenMintOuts: [],
      tokenAccountOuts: [],
      // TODO: the following is a bit ghetto, but basically will ensure that the token account is only created if the recipient
      // is not the caller. This ensures that the token account is not created again because its probably created by a former action/ token analyzer
      instructions:
        createTokInstr && !recipient.equals(provider.wallet.publicKey) // TODO: think of a better way to do this with tokens
          ? [createTokInstr]
          : undefined,
    },
  };
};

export const TransferAction: ActionLib<BuildTransferArgs> = {
  buildIsolatedAction: buildIsolatedTransfer,
  actionTypeUID,
  expectedNumberOfOutMints: 0,
  idl: IDL as Idl,
};
