import { Provider } from "@project-serum/anchor";
import { PublicKey } from "@solana/web3.js";
import BN from "bn.js";
import {
  Action,
  ActionMetadata,
  BuildActionMap,
  Construction,
  errors,
  NextNode,
  NonUserSpecificConstruction,
} from "..";
import { buildSeqListOfActionCalls } from "../graph-utils";
import { deepCloneObject } from "../utils/object";
import {
  getTokenAccountUsedByMalloc,
  PreferredTokenAccounts,
} from "../utils/tokens";

const getPreviousActions = (
  actionIdx: number,
  actions: (Action | undefined)[]
): { idx: number; idxInNextNodes: [number, number] }[] => {
  // First add indices to each action
  // Then remove the undefined actions
  // Then search if the action has the actionIdx
  return actions
    .map((a, i) => {
      return a ? { ...a, idx: i } : a;
    })
    .filter((a) => !!a)
    .map((a: Action & { idx: number }) => {
      const idxInNextNodes: [number, number] = [-1, -1];
      // TODO: note this assumes that each action is only called once from a prior
      for (let i = 0; i < a.actionMetadata.nextNodes.length; i++) {
        idxInNextNodes[1] = a.actionMetadata.nextNodes[i]
          .map((item) => item.actionIdx)
          .indexOf(actionIdx);
        if (idxInNextNodes[1] !== -1) {
          idxInNextNodes[0] = i;
          break;
        }
      }
      if (idxInNextNodes[0] === -1 || idxInNextNodes[1] === -1)
        return undefined;
      return {
        idx: a.idx,
        idxInNextNodes,
      };
    })
    .filter((i) => !!i);
};

/**
 * Get the mint from prior actions
 *
 * This assumes that the action given is not an initial action
 */
const getMintAccountFromPriorActions = (
  actionIdx: number,
  actions: (Action | undefined)[]
): string => {
  const previousActions = getPreviousActions(actionIdx, actions);
  const mints = previousActions.map((prev) => {
    const action = actions[prev.idx] as Action; // assume its not undefined because getPreviousAction ensures its not undefined
    const mint = action.tokenMintOuts[prev.idxInNextNodes[0]];
    return mint.toBase58();
  });
  if (mints.length < 1)
    throw `Expected to find a previous action for a non initial action`;
  const firstMint = mints[0];
  if (!mints.every((mint) => mint === firstMint))
    throw `Expected all previous mints to be the same for the action`;
  return firstMint;
};

export const buildConstructionFromNonUserSpecific = async (
  construction: NonUserSpecificConstruction,
  buildActionMap: BuildActionMap,
  initTokenAccount: string,
  initMintAccount: string,
  provider: Provider,
  preferredTokenAccounts?: PreferredTokenAccounts
): Promise<Construction> => {
  const orderActionsHit = buildSeqListOfActionCalls(
    construction.actionDatas.map((a) => a.actionMetadata),
    construction.initialActionIndices
  );

  if (orderActionsHit.length !== construction.actionDatas.length)
    throw errors.newBuilderError(
      `The order of actions hit should match the length of actions`
    );

  const actions: (Action | undefined)[] = [
    ...Array(construction.actionDatas.length),
  ];
  for (let i = 0; i < orderActionsHit.length; i++) {
    const actionIdx = orderActionsHit[i];
    const mintAccount = construction.initialActionIndices.includes(actionIdx)
      ? initMintAccount
      : getMintAccountFromPriorActions(actionIdx, actions);
    const actionData = construction.actionDatas[actionIdx];
    const buildFN =
      buildActionMap[actionData.actionTypeUID].buildIsolatedAction;
    if (!buildFN)
      throw errors.newBuilderError(
        `Could not find a builder function for action with UID ${actionData.actionTypeUID}`
      );
    const tokenAccount = construction.initialActionIndices.includes(actionIdx)
      ? initTokenAccount
      : (
          await getTokenAccountUsedByMalloc(
            provider.wallet.publicKey,
            new PublicKey(mintAccount),
            preferredTokenAccounts
          )
        ).toBase58();
    try {
      const { isolatedAction } = await buildFN(
        actionData.buildActionInputs,
        mintAccount,
        tokenAccount,
        actionData.actionPID,
        provider,
        {
          preferredTokenAccounts,
        }
      );
      const action = {
        ...isolatedAction,
        actionMetadata: actionData.actionMetadata,
        actionTypeUID: actionData.actionTypeUID,
      };
      actions[actionIdx] = action;
    } catch (e) {
      throw errors.newActionBuilderError(
        actionData.actionTypeUID,
        actionIdx,
        e
      );
    }
  }

  return {
    actions,
    initialActionIndices: construction.initialActionIndices,
    initialSplits: construction.initialSplits,
    amount: new BN(construction.amount),
  };
};

const offsetNextNodes = (
  nextNodes: ActionMetadata["nextNodes"],
  offset: number
): ActionMetadata["nextNodes"] =>
  nextNodes.map((nextNodeSet) =>
    nextNodeSet.map((n) => {
      return {
        actionIdx: n.actionIdx + offset,
        fraction: n.fraction,
      } as NextNode;
    })
  );

/**
 * Merge two constructions into 1. One is the parent and keeps its initial indices and amounts,
 * the other discards the amount value and initial indices/ splits and replaces them with the specified inputs
 *
 * @param appendChildInitToParent - where to append the initial action indices of the child construction
 * to the parent.
 */
export const mergeConstructions = (
  parentConstruction: NonUserSpecificConstruction,
  childConstruction: NonUserSpecificConstruction,
  appendChildInitToParent: {
    appendToInitial?: boolean;
    indexInParent?: {
      actionIdx: number;
      nextNodeIdx: number;
    };
    splitFrac: number | string;
  }[]
): NonUserSpecificConstruction => {
  // deep copy the child and parent
  const childConstructionCloned = deepCloneObject(childConstruction);
  const parentConstructionClone = deepCloneObject(parentConstruction);

  if (
    appendChildInitToParent.length! ==
      childConstructionCloned.initialActionIndices.length &&
    appendChildInitToParent.length !==
      childConstructionCloned.initialSplits.length
  ) {
    throw `Expected the info to append the child to the parent to provide the same number of indices as the number of initial splits in the child`;
  }
  const newActionDatasArray = [
    ...parentConstructionClone.actionDatas,
    ...childConstructionCloned.actionDatas.map((a) => {
      return {
        ...a,
        actionMetadata: {
          ...a.actionMetadata,
          nextNodes: offsetNextNodes(
            a.actionMetadata.nextNodes,
            parentConstructionClone.actionDatas.length
          ),
        },
      };
    }),
  ];

  const childInitialActionsToAdd = [];
  for (let i = 0; i < appendChildInitToParent.length; i++) {
    if (appendChildInitToParent[i].appendToInitial) {
      childInitialActionsToAdd.push({
        idx: i + parentConstructionClone.actionDatas.length,
        fraction: appendChildInitToParent[i].splitFrac,
      });
    } else if (appendChildInitToParent[i].indexInParent) {
      newActionDatasArray[
        appendChildInitToParent[i].indexInParent.actionIdx
      ].actionMetadata.nextNodes[
        appendChildInitToParent[i].indexInParent.nextNodeIdx
      ].push({
        fraction: appendChildInitToParent[i].splitFrac,
        actionIdx: i + parentConstructionClone.actionDatas.length,
      });
    } else {
      throw `Expected the appendChildInitToParent to either append to initial or contain info as to where to append in the parent`;
    }
  }

  const newInitialActionIndices = [
    ...parentConstructionClone.initialActionIndices,
    ...childInitialActionsToAdd.map((c) => c.idx),
  ];
  const newIninitialSplits = [
    ...parentConstructionClone.initialSplits,
    ...childInitialActionsToAdd.map((c) => c.fraction),
  ];
  return {
    actionDatas: newActionDatasArray,
    initialActionIndices: newInitialActionIndices,
    initialSplits: newIninitialSplits,
    amount: parentConstructionClone.amount,
  };
};
