const actionTypeUID = "MALLOC_SPL_SWAP";

interface SwapInput {
  // outTokenAccount: string; //TODO: hmmm...
  outMint: string;
  serumDexProgramId: string;
  rent: string;
  openOrdersAccount?: string;
}

type BuildIsolatedSwapArgs = SwapInput & {
  minOut: string;
};
export const buildIsolatedSwapAction: BuildIsolatedActionFn<BuildIsolatedSwapArgs> =
  async (
    inp: BuildIsolatedSwapArgs,
    inMint: string,
    inTokenAccount: string,
    swapPID: string,
    provider: Provider,
    opts?: BuildIsolatedActionFnOpts<BuildIsolatedSwapArgs>
  ) => {
    const outTokenAccount = await utils.tokens.getTokenAccountUsedByMalloc(
      provider.wallet.publicKey,
      new PublicKey(inp.outMint),
      opts?.preferredTokenAccounts
    );
    const swapInfo = await buildSwapAccounts(
      inp,
      inMint,
      inTokenAccount,
      outTokenAccount.toBase58(),
      provider
    );

    const inMintData = await getMintInfo(provider, new PublicKey(inMint));
    const outMinExpanded = utils.tokens.fromReadableNumber(
      parseFloat(inp.minOut),
      inMintData.decimals
    );
    return {
      isolatedAction: {
        addDefaultTokAccounts: false,
        actionData: packSwapData(
          SwapInstructionByte.BasicSwap,
          swapInfo.side,
          new u64(outMinExpanded)
        ),
        tokenAccountOuts: [outTokenAccount],
        actionAccounts: swapAccountsToActionAccounts(
          swapInfo.swapAccounts,
          swapInfo.side
        ),
        actionProgram: new PublicKey(swapPID),
        tokenMintIn: new PublicKey(inMint),
        tokenMintOuts: [new PublicKey(inp.outMint)],
        preActionTxs: swapInfo.openOrderInitTx
          ? [swapInfo.openOrderInitTx]
          : [],
      },
    };
  };

export const buildSwapAccounts = async (
  inp: SwapInput,
  inMint: string,
  inTokenAccount: string,
  outTokenAccount: string,
  provider: Provider
): Promise<{
  swapAccounts: SwapAccounts;
  openOrderInitTx?: SendTxRequest;
  market: Market;
  side: Side;
}> => {
  const serumDexId = new PublicKey(inp.serumDexProgramId);
  const marketRet = await getMarket(
    provider.connection,
    new PublicKey(inMint),
    new PublicKey(inp.outMint),
    serumDexId
  );
  if (!marketRet) throw "No markets found";

  const { side, pk: marketPk } = marketRet;
  const marketSel = await Market.load(
    provider.connection,
    marketPk,
    {},
    serumDexId
  );

  const openOrderAccountData = await getOpenOrdersAccount(
    provider.connection,
    marketSel,
    provider.wallet.publicKey,
    new PublicKey(inp.serumDexProgramId)
  );

  const [vaultSigner, _] = await getVaultOwnerAndNonce(
    marketSel.decoded.ownAddress,
    new PublicKey(inp.serumDexProgramId)
  );

  const coinWallet = side === Side.Bid ? outTokenAccount : inTokenAccount;
  const pcWallet = side === Side.Bid ? inTokenAccount : outTokenAccount;

  return {
    market: marketSel,
    swapAccounts: {
      market: {
        market: marketSel.decoded.ownAddress,
        requestQueue: marketSel.decoded.requestQueue,
        eventQueue: marketSel.decoded.eventQueue,
        bids: marketSel.decoded.bids,
        asks: marketSel.decoded.asks,
        coinVault: marketSel.decoded.baseVault,
        pcVault: marketSel.decoded.quoteVault,
        vaultSigner: vaultSigner,
        // User params.
        openOrders: new PublicKey(
          inp.openOrdersAccount || openOrderAccountData.openOrderPk
        ),
        orderPayerTokenAccount: new PublicKey(inTokenAccount),
        coinWallet: new PublicKey(coinWallet),
      },
      pcWallet: new PublicKey(pcWallet),
      authority: provider.wallet.publicKey,
      dexProgram: new PublicKey(inp.serumDexProgramId),
      tokenProgram: new PublicKey(TOKEN_PROGRAM_ID),
      rent: new PublicKey(inp.rent),
    },

    openOrderInitTx: inp.openOrdersAccount
      ? undefined
      : openOrderAccountData.openOrderTx,
    side,
  };
};

/**
 * get the open order accounts associated with the market and owner
 *
 * If none exist, create a new account and return a tx to create/ init the account as well
 */
const getOpenOrdersAccount = async (
  connection: Connection,
  market: Market,
  owner: PublicKey,
  dexPID: PublicKey
): Promise<{
  openOrderPk: PublicKey;
  openOrderTx?: SendTxRequest;
}> => {
  const openOrders = await OpenOrders.findForMarketAndOwner(
    connection,
    market.address,
    owner,
    dexPID
  );
  if (openOrders.length === 0) {
    const openOrderAccount = new Account(new Keypair().secretKey);
    const openOrdersTx = new Transaction();
    openOrdersTx.add(
      await OpenOrders.makeCreateAccountTransaction(
        connection,
        market.decoded.ownAddress,
        owner,
        openOrderAccount.publicKey,
        dexPID
      )
    );

    return {
      openOrderPk: openOrderAccount.publicKey,
      openOrderTx: {
        tx: openOrdersTx,
        signers: [openOrderAccount],
      },
    };
  } else {
    return {
      openOrderPk: openOrders[0].publicKey,
    };
  }
};

// TODO: this just takes the first market available... have some sorta best market in the future?
/**
 * base mint is the in mint
 * quote mint is the out mint
 */
export const getMarket = async (
  connection: Connection,
  inMint: PublicKey,
  outMint: PublicKey,
  serumDexProgramId: PublicKey
) => {
  // TODO: slightly confused about side
  let side = Side.Ask;
  let marketPks = await getMarketsFromBaseMinttoQuoteMint(
    connection,
    inMint,
    outMint,
    serumDexProgramId
  );

  // Try the other market side
  if (!marketPks || marketPks.length === 0) {
    marketPks = await getMarketsFromBaseMinttoQuoteMint(
      connection,
      outMint,
      inMint,
      serumDexProgramId
    );
    side = Side.Bid;
  }
  if (!marketPks || marketPks.length === 0) return null;
  return { pk: marketPks[0], side };
};

export const packSwapData = (
  instrByte: SwapInstructionByte,
  side: Side,
  minOut: u64
): Buffer => {
  const packedMinOut = minOut.toArrayLike(Buffer, "be", 8);
  return Buffer.from(new Uint8Array([instrByte, side, ...packedMinOut]));
};

export const swapAccountsToActionAccounts = (
  swapAccounts: SwapAccounts,
  side: Side,
  referralAccount?: PublicKey
): AccountsArray => {
  const tokenAccount =
    side === Side.Bid ? swapAccounts.pcWallet : swapAccounts.market.coinWallet;
  const outAccount =
    side === Side.Bid ? swapAccounts.market.coinWallet : swapAccounts.pcWallet;
  const accounts = [
    {
      isWriteable: true,
      address: tokenAccount,
    },
    {
      address: swapAccounts.tokenProgram,
    },
    {
      address: swapAccounts.authority,
      isSigner: true,
    },
    {
      address: swapAccounts.rent,
    },
    {
      address: swapAccounts.dexProgram,
    },
    {
      address: outAccount,
      isWriteable: true,
    },
    {
      address: swapAccounts.market.market,
      isWriteable: true,
    },
    {
      address: swapAccounts.market.openOrders,
      isWriteable: true,
    },
    {
      address: swapAccounts.market.requestQueue,
      isWriteable: true,
    },
    {
      address: swapAccounts.market.eventQueue,
      isWriteable: true,
    },
    {
      address: swapAccounts.market.bids,
      isWriteable: true,
    },
    {
      address: swapAccounts.market.asks,
      isWriteable: true,
    },
    {
      address: swapAccounts.market.orderPayerTokenAccount,
      isWriteable: true,
    },
    {
      address: swapAccounts.market.coinVault,
      isWriteable: true,
    },
    {
      address: swapAccounts.market.pcVault,
      isWriteable: true,
    },
    {
      address: swapAccounts.market.vaultSigner,
    },
  ];
  if (referralAccount) {
    accounts.push({
      address: referralAccount,
      isWriteable: true,
    });
  }
  return accounts;
};

export async function getVaultOwnerAndNonce(
  marketPublicKey: PublicKey,
  dexProgramId: PublicKey
): Promise<[PublicKey, BN]> {
  const nonce = new BN(0);
  while (nonce.toNumber() < 255) {
    try {
      const vaultOwner = await PublicKey.createProgramAddress(
        [marketPublicKey.toBuffer(), nonce.toArrayLike(Buffer, "le", 8)],
        dexProgramId
      );
      return [vaultOwner, nonce];
    } catch (e) {
      nonce.iaddn(1);
    }
  }
  throw new Error("Unable to find nonce");
}

const getMarketsFromBaseMinttoQuoteMint = async (
  connection: Connection,
  baseMint: PublicKey,
  quoteMint: PublicKey,
  serumProgId: PublicKey
): Promise<PublicKey[]> => {
  const layout = await Market.getLayout(serumProgId);

  return (
    await Market.findAccountsByMints(
      connection,
      baseMint,
      quoteMint,
      serumProgId
    )
  ).map((e) => e.publicKey);
};

const getAllMarkets = async (conn: Connection, progId: PublicKey) => {
  Market.findAccountsByMints;
};

export const getDemoTokenInfo = async (
  provider: Provider,
  fakeUsdc: PublicKey | string,
  fakeTokA: PublicKey | string
): Promise<TokenInfo[]> => {
  const usdcMint = new PublicKey(fakeUsdc);
  const aMint = new PublicKey(fakeTokA);
  const usdcInfo = await getMintInfo(provider, usdcMint);
  const aInfo = await getMintInfo(provider, aMint);
  if (!usdcInfo || !aInfo) {
    throw "Expected info for these mints";
  }
  return [
    {
      ...usdcInfo,
      chainId: -1,
      name: "FAKE USDC",
      symbol: "FUSDC",
      address: new PublicKey(fakeUsdc).toBase58(),
    },
    {
      ...aInfo,
      chainId: -1,
      name: "FAKE: A TOKEN",
      symbol: "FA TOKEN",
      address: new PublicKey(fakeTokA).toBase58(),
    },
  ];
};

export const getMintInfo = async (
  provider: Provider,
  mintAddr: PublicKey
): Promise<MintInfo | null> => {
  const account = await provider.connection.getAccountInfo(mintAddr);
  if (!account) return null;
  return parseMintAccount(account.data);
};

