import { ActionLib, BuildIsolatedActionFn, utils } from "@malloc/sdk";
import {
  PublicKey,
  SYSVAR_CLOCK_PUBKEY,
  TransactionInstruction,
} from "@solana/web3.js";
import { Idl, Provider } from "../../../../anchor/ts/src";
import * as solend from "@solendprotocol/common";
import {
  getTokenAccountUsedByMalloc,
  PreferredTokenAccounts,
} from "../../../malloc-core/ts-packages/malloc-sdk/lib/utils/tokens";
import {
  depositReserveLiquidityInstruction,
  refreshReserveInstruction,
} from "@solana/spl-token-lending";
import { TOKEN_PROGRAM_ID } from "@solana/spl-token";
import { isWeakMap } from "util/types";
import IDL from "../../target/idl/solend_action.json";

type BuildSolendArgs = {};

const actionTypeUID = "MALLOC_SPL_SOLEND";

// TODO: this only supports devnet right now
const buildIsolatedSolend: BuildIsolatedActionFn<BuildSolendArgs> = async (
  inp: BuildSolendArgs,
  mintAccount: string,
  inputTokenAccount: string,
  solendActionPID,
  provider: Provider,
  preferredTokenAccounts?: PreferredTokenAccounts
) => {
  const mintIn = new PublicKey(mintAccount);
  const tokenAccountIn = new PublicKey(inputTokenAccount);

  const solendConfig = solend.devnetConfig;
  const assetSymbol = solendConfig.assets.find(
    (asset) => asset.mintAddress === mintAccount
  )?.symbol;
  if (!assetSymbol) {
    throw `Looks like solend does not support token of mint ${mintAccount}`;
  }

  const mainMarket = solendConfig.markets[0];
  const reserve = mainMarket.reserves.find((r) => r.asset === assetSymbol);

  if (!reserve) {
    throw `Looks like Solend does not have a market for ${assetSymbol}`;
  }

  const collateralMint = new PublicKey(reserve.collateralMintAddress);
  const destinationCollateralAccount = await getTokenAccountUsedByMalloc(
    provider.wallet.publicKey,
    collateralMint,
    preferredTokenAccounts
  );

  const oracle = solendConfig.oracles.assets.find(
    (o) => o.asset === assetSymbol
  );
  if (!oracle) throw `Could not find the oracle for ${assetSymbol}`;
  const { data: refreshData } = refreshReserveInstruction(
    new PublicKey(reserve.address),
    new PublicKey(oracle.priceAddress)
  );
  return {
    isolatedAction: {
      tokenMintIn: mintIn,
      actionData: Buffer.from([]),
      actionProgram: new PublicKey(solendActionPID),
      actionAccounts: [
        {
          address: new PublicKey(solendConfig.programID),
        },
        {
          address: mintIn,
        },
        {
          address: tokenAccountIn,
          isWriteable: true,
        },
        { address: destinationCollateralAccount, isWriteable: true },
        { address: new PublicKey(reserve.address), isWriteable: true },
        { address: new PublicKey(reserve.liquidityAddress), isWriteable: true },
        { address: collateralMint, isWriteable: true },
        { address: new PublicKey(mainMarket.address), isWriteable: false },
        { address: new PublicKey(mainMarket.authorityAddress) },
        { address: provider.wallet.publicKey, isSigner: true },
        { address: SYSVAR_CLOCK_PUBKEY },
        { address: TOKEN_PROGRAM_ID },
      ],
      instructions: [
        new TransactionInstruction({
          data: refreshData,

          programId: new PublicKey(solendConfig.programID),
          keys: [
            {
              pubkey: new PublicKey(reserve.address),
              isWritable: true,
              isSigner: false,
            },
            {
              pubkey: new PublicKey(oracle.priceAddress),
              isWritable: false,
              isSigner: false,
            },
            {
              pubkey: new PublicKey(oracle.switchboardFeedAddress),
              isWritable: false,
              isSigner: false,
            },
            {
              pubkey: SYSVAR_CLOCK_PUBKEY,
              isSigner: false,
              isWritable: false,
            },
          ],
        }),
      ],
      additionalSigners: [],
      tokenMintOuts: [collateralMint],
      tokenAccountOuts: [destinationCollateralAccount],
    },
  };
};

export const SolendAction: ActionLib<BuildSolendArgs> = {
  buildIsolatedAction: buildIsolatedSolend,
  actionTypeUID,
  expectedNumberOfOutMints: 1,
  idl: IDL as Idl,
};
