import { MintInfo, u64 } from "@solana/spl-token";
import { TokenInfo } from "@solana/spl-token-registry";
import { PublicKey } from "@solana/web3.js";
import { FlowElement } from "react-flow-renderer";
import internal from "stream";

export enum InputClassNames {
  actionVertex = "ACTION_VERTEX",
  tokenAccount = "TOKEN_ACCOUNT",
  mintAccount = "MINT_ACCOUNT",
  tokenAccountOnlyInit = "TOKEN_ACCOUNT_ONLY_INIT",
  mintAccountOnlyInit = "MINT_ACCOUNT_ONLY_INIT",
  wholeNumber = "WHOLE_NUMBER",
  fractionNumber = "FRACTION_NUMBER",
  other = "OTHER",
}

export type SPLToken = { account: string } & {
  info: Partial<TokenInfo> & { decimals: number; mint: PublicKey };
  balance: string | u64 | null;
};

export type NextAction = [
  { value: string; readOnly?: boolean; className?: string },
  { value: string; readOnly?: boolean; className?: string }
];

export type Input = [
  { value: string; readOnly: true },
  { value: string; className?: string }
][];
export type SourceNodeInput = [
  [{ value: string; readOnly: true }, { value: string; className?: string }],
  [{ value: string; readOnly: true }, { value: string; className?: string }]
];
export type ActionEdge = { id: string; amount: number };
export type NodeType = { input: Input; next_actions: NextAction[][] };
export type GraphType = { [id: string]: NodeType };
export type HiddenInputData = { [id: string]: any };
