import React, { useState, FC } from "react";
import Spreadsheet from "react-spreadsheet";

import {
  Input,
  ActionEdge,
  NodeType,
  GraphType,
  NextAction,
  InputClassNames,
  SourceNodeInput,
} from "src/interfaces";
import { useStore } from "react-stores";
import {
  downloadGlobalStore,
  globalStore,
  INIT_NEXT_ACTION,
  setGlobalStoreFromFile,
  SOURCE_NODE_ID,
} from "../globalStore";
import { compileAndRunMallocSDK } from "src/utils/sdk-interpelator";
import { useAnchorWallet, useWallet } from "@solana/wallet-adapter-react";
import { getSourceNodeAccount } from "src/utils/graph";
import { MallocSPLBuildActionMap } from "@malloc/spl";
import { TableCellEditor } from "./table/table-cells";
import { performUsdcAirdrop } from "./usdc-airdrop/perform-usdc-airdrop";
import { saveStringToFile } from "src/utils/files";
import { Satellite } from "@material-ui/icons";
import { getMintInfo, getTokenAccountInfo } from "src/utils/spl-tokens";
import { getProvider } from "src/utils/anchor";
import { PublicKey } from "@solana/web3.js";
import {
  ActionTypes,
  ACTION_TYPES,
  getActionTypeUIDFromVertex,
  getInitInputForActionType,
} from "src/utils/action-types";

const NodeSelector: FC = () => {
  const state = useStore(globalStore);
  const actions = ACTION_TYPES;

  return (
    <div className="card">
      <h2>Node Id</h2>

      <select
        id="node-selector"
        className="form-select"
        size={5}
        aria-label="size 3 select example"
        onChange={(e) => {
          globalStore.setState({ selected: e.target.value });
        }}
      >
        {Object.keys(state.graph).map(function (key, value) {
          return <option value={key}>{key}</option>;
        })}
      </select>
      <br />

      <div className="row">
        <div className="col-10">
          <select
            className="form-select"
            aria-label="Default select example"
            onChange={(e) => {
              globalStore.setState({ new_action: e.target.value });
            }}
          >
            {actions.map((value, index) => {
              return <option value={value}>{value}</option>;
            })}
          </select>
        </div>
        <div className="col-2 justify-content-center">
          <button
            onClick={() => {
              let node_id = state.new_action + "-" + state.counter.toString();
              const init = getInitInputForActionType(
                state.new_action as ActionTypes
              );
              let new_node: NodeType = {
                input: init.input,
                // TODO: make a seperate fn
                next_actions: [
                  ...Array(
                    MallocSPLBuildActionMap[
                      getActionTypeUIDFromVertex(
                        state.new_action as ActionTypes
                      )
                    ].expectedNumberOfOutMints
                  ),
                ].map((i) => INIT_NEXT_ACTION),
              };
              state.graph[node_id] = new_node;
              state.hiddenInputData[node_id] = init.hiddenData || {};
              globalStore.setState({
                ...state,
                // selected: node_id,
                counter: state.counter + 1,
                graph: { ...state.graph },
              });
            }}
          >
            add
          </button>
        </div>
      </div>
    </div>
  );
};

export default NodeSelector;
