import {
  Action,
  utils as mallocUtils,
  ConstructionInstr,
  builder,
  MallocSdk,
  NonUserSpecificConstruction,
  NonUserSpecificConstructionAction,
  ActionMetadata,
  utils,
} from "@malloc/sdk";
import { BN, Provider } from "@project-serum/anchor";
import { AnchorWallet } from "@solana/wallet-adapter-react";
import { PublicKey } from "@solana/web3.js";
import { getConstants } from "src/config/constants";
import { getGlobalStore, globalStore, SOURCE_NODE_ID } from "src/globalStore";
import {
  GraphType,
  HiddenInputData,
  Input,
  NodeType,
  SourceNodeInput,
} from "src/interfaces";
import { getProvider } from "./anchor";
import { ErrorGenerator, handleError, newFrontendError } from "./errors";
import {
  getAllVertices,
  getNextVertices,
  getSourceNode,
  getSourceNodeAccount,
  getSourceNodeAmount,
  getVertexType,
} from "./graph";
import { getMintInfo, getTokenAccountInfo } from "./spl-tokens";
import {
  MallocSPLBuildActionMap,
  SwapAction,
  TransferAction,
} from "@malloc/spl";
import { GroupAddSharp } from "@material-ui/icons";
import {
  getActionPIDFromVertex,
  getActionTypeUIDFromVertex,
} from "./action-types";

const PID = new PublicKey(getConstants().MALLOC_PROGRAM_ID);
const getMallocSdk = (wallet: AnchorWallet) =>
  new MallocSdk(PID, getProvider(wallet));

const getAllIncomingVertexIds = (actionId: string, graph: GraphType) =>
  getAllVertices(graph).filter((v) =>
    getNextVertices(graph, v).some((nextEdgeSet) =>
      nextEdgeSet.map((i) => i.id).includes(actionId)
    )
  );

const getInputFromActionInput = (
  actionInput: Input,
  hiddenInputData: any
): any => {
  const ret = {} as any;
  actionInput.map((inpItem) => {
    ret[inpItem[0].value] = inpItem[1].value;
  });
  return { ...ret, ...hiddenInputData };
};

const getNextNodes = (
  allActionIds: string[],
  actionId: string,
  graph: GraphType
): ActionMetadata["nextNodes"] => {
  const nextActions = getNextVertices(graph, actionId);

  const groupedByMints: ActionMetadata["nextNodes"] = nextActions.map(
    (nextActionSet) => {
      return nextActionSet.map((next) => {
        return {
          actionIdx: allActionIds.indexOf(next.id),
          fraction: next.amount,
        };
      });
    }
  );

  return groupedByMints;
};

const getActionData = (
  actionId: string,
  allActionIds: string[],
  graph: GraphType,
  hiddenInputData: HiddenInputData
): NonUserSpecificConstructionAction => {
  const incomingVertices = getAllIncomingVertexIds(actionId, graph);

  const nextNodes: ActionMetadata["nextNodes"] = getNextNodes(
    allActionIds,
    actionId,
    graph
  );

  const vertexType = getVertexType(actionId);
  return {
    actionTypeUID: getActionTypeUIDFromVertex(vertexType),
    buildActionInputs: getInputFromActionInput(
      graph[actionId].input,
      hiddenInputData[actionId]
    ),
    actionPID: getActionPIDFromVertex(vertexType),
    actionMetadata: {
      ripeAmount: incomingVertices.length,
      nextNodes,
    },
  };
};

const _buildNonUserSpecificConstruction = async (
  g: GraphType,
  hiddenInputDatas: HiddenInputData,
  actionVertices: string[],
  initialSplits: number[],
  initialActionIndices: number[],
  amount: string
): Promise<NonUserSpecificConstruction> => {
  const vertices = getAllVertices(g);

  return {
    actionDatas: actionVertices.map((v) =>
      getActionData(v, actionVertices, g, hiddenInputDatas)
    ),
    initialSplits,
    initialActionIndices,
    amount,
  };
};

const getInitialInfo = async (
  g: GraphType,
  inAccount: PublicKey,
  actionVertices: string[],
  provider: Provider
) => {
  const sourceNode = getSourceNode(g);
  const tokenInInfo = await getTokenAccountInfo(provider, inAccount);
  if (!tokenInInfo) {
    throw ErrorGenerator.TOKEN_ACCOUNT_DOES_NOT_EXIST(inAccount);
  }

  const mint = tokenInInfo.mint;
  const mintInfo = await getMintInfo(provider, mint);

  if (!mintInfo) {
    throw ErrorGenerator.MINT_ACCOUNT_DOES_NOT_EXIST(mint, inAccount);
  }

  let amountInF: number;
  try {
    amountInF = parseFloat(
      getSourceNodeAmount(sourceNode.input as SourceNodeInput)
    );
  } catch (e) {
    throw ErrorGenerator.INVALID_AMOUNT_INPUT(
      getSourceNodeAmount(sourceNode.input as SourceNodeInput)
    );
  }
  const amountIn = utils.tokens.fromReadableNumber(
    amountInF,
    mintInfo.decimals
  );
  const nextFromSource = getNextVertices(g, SOURCE_NODE_ID)[0];

  return {
    amount: amountIn,
    initialMint: mint,
    initialSplits: nextFromSource.map((n) => n.amount),
    initialActionIndices: nextFromSource.map((n) =>
      actionVertices.indexOf(n.id)
    ),
  };
};

const buildConstruction = async (
  wallet: AnchorWallet,
  preferredTokenAccounts?: mallocUtils.tokens.PreferredTokenAccounts
): Promise<ConstructionInstr> => {
  const { graph, hiddenInputData: hiddenData } = getGlobalStore();

  let inAccount: PublicKey;
  try {
    inAccount = new PublicKey(
      getSourceNodeAccount(getSourceNode(graph).input as SourceNodeInput)
    );
  } catch {
    throw newFrontendError("Please provide a valid source token account");
  }

  const provider = getProvider(wallet);
  const vertices = getAllVertices(graph);
  const actionVertices = vertices.slice(1);
  const { initialSplits, initialActionIndices, initialMint, amount } =
    await getInitialInfo(graph, inAccount, actionVertices, provider);

  // Get the non specific
  const nonUserSpecific = await _buildNonUserSpecificConstruction(
    graph,
    hiddenData,
    actionVertices,
    initialSplits,
    initialActionIndices,
    amount
  );

  const construction = await builder.buildConstructionFromNonUserSpecific(
    nonUserSpecific,
    MallocSPLBuildActionMap,
    inAccount.toBase58(),
    initialMint.toBase58(),
    provider
  );

  return {
    actions: construction.actions,
    initialSplits: construction.initialSplits,
    amount: construction.amount,
    initialActionIndices: construction.initialActionIndices,
    amountInAccount: inAccount,
    actionMap: MallocSPLBuildActionMap,
    opts: { preferredTokenAccounts },
  };
};

const run = async (
  inp: ConstructionInstr,
  wallet: AnchorWallet
): Promise<string[]> => {
  const sdk = getMallocSdk(wallet);
  const construction = await sdk.compileConstruction(inp);
  return await sdk.runConstruction(construction, {
    splitPrepareAndActions: {
      prepareCommitment: {
        skipPreflight: false,
        preflightCommitment: "single",
        commitment: "single",
      },
    },
    commitment: {
      skipPreflight: false,
      preflightCommitment: "single",
      commitment: "single",
    },
  });
};

export const compileAndRunMallocSDK = async (wallet: AnchorWallet) => {
  try {
    const buildInst = await buildConstruction(wallet);
    const txs = await run(buildInst, wallet);
    alert(`DONE AND SUCCESSFUL with tx: ${txs}`);
  } catch (e) {
    handleError(e);
  }
};
