import { useEffect, useState } from "react";
import styled from "styled-components";
import Countdown from "react-countdown";
import { Button, CircularProgress, Snackbar } from "@material-ui/core";
import Alert from "@material-ui/lab/Alert";

import * as anchor from "@project-serum/anchor";

import { LAMPORTS_PER_SOL } from "@solana/web3.js";

import { useWallet } from "@solana/wallet-adapter-react";
import { WalletDialogButton } from "@solana/wallet-adapter-material-ui";

import {
  awaitTransactionSignatureConfirmation,
  shortenAddress,
} from "./utils/malloc";
import { useStore } from "react-stores";
import LoadingTX from "./components/tx-handler/LoadingTX";
import TableGUI from "./components/tablegui";

const ConnectButton = styled(WalletDialogButton)``;

const CounterText = styled.span``; // add your styles here

export interface HomeProps {
  config: anchor.web3.PublicKey;
  connection: anchor.web3.Connection;
  txTimeout: number;
}

const Home = (props: HomeProps) => {
  const [balance, setBalance] = useState<number>();
  const [isActive, setIsActive] = useState(false); // true when countdown completes
  const [isSoldOut, setIsSoldOut] = useState(false); // true when items remaining is zero
  const [isMinting, setIsMinting] = useState(false); // true when user got to press MINT

  const [alertState, setAlertState] = useState<AlertState>({
    open: false,
    message: "",
    severity: undefined,
  });

  const wallet = useWallet();

  useEffect(() => {
    (async () => {
      if (wallet?.publicKey) {
        const balance = await props.connection.getBalance(wallet.publicKey);
        setBalance(balance / LAMPORTS_PER_SOL);
      }
    })();
  }, [wallet, props.connection]);

  return (
    <main>
      <nav className="navbar navbar-light bg-light">
        {/* <div className="col"></div> */}
        <div className="col-4">
          <h1 className="text-start"> MALLOC Protocol</h1>
        </div>
        <div className="col-3">
          {wallet.connected && (
            <div>
              <p style={{ marginTop: 0 }}>
                Address: {shortenAddress(wallet.publicKey?.toBase58() || "")} ,
                Balance: {(balance || 0).toLocaleString()} SOL
              </p>
            </div>
          )}
          {!wallet.connected && <ConnectButton>Connect Wallet</ConnectButton>}
        </div>
        <div className="col-1">
          <a
            href="https://github.com/Lev-Stambler/malloc-solana2/blob/master/Malloc_User_Guide.md"
            target="_blank"
          >
            User Guide
          </a>
        </div>
        <div className="col-1">
          <a
            href="https://github.com/Lev-Stambler/malloc-solana2"
            target="_blank"
          >
            Github
          </a>
        </div>
        <div className="col-1">
          <a href="https://twitter.com/MallocProtocol" target="_blank">
            Twitter
          </a>
        </div>
        <div className="col-1">
          <a href="mailto:mallocprotocol@gmail.com" target="_blank">
            Contact
          </a>
        </div>
      </nav>
      <Snackbar
        open={alertState.open}
        autoHideDuration={6000}
        onClose={() => setAlertState({ ...alertState, open: false })}
      >
        <Alert
          onClose={() => setAlertState({ ...alertState, open: false })}
          severity={alertState.severity}
        >
          {alertState.message}
        </Alert>
      </Snackbar>

      <br />
      {wallet.connected && <TableGUI />}
    </main>
  );
};

interface AlertState {
  open: boolean;
  message: string;
  severity: "success" | "info" | "warning" | "error" | undefined;
}

const renderCounter = ({ days, hours, minutes, seconds, completed }: any) => {
  return (
    <CounterText>
      {hours} hours, {minutes} minutes, {seconds} seconds
    </CounterText>
  );
};

export default Home;
