export interface RunConstructionOpts {
  commitment?: web3.ConfirmOptions;
  splitPrepareAndActions?: {
    prepareCommitment?: web3.ConfirmOptions;
    onPrepareAccountsDone?: (txHashes: string[]) => Promise<any>;
  };
}

export interface ConstructionCompiled {
  actionMap: BuildActionMap;
  txsPrepare: SendTxRequest[];
  txsActionCalls: SendTxRequest[];
  construction: Signer;
  resultAccounts: Signer[];
  tokenAccounts: AccountByMint;
  actionPIDToUID: ActionPIDToActionUID;
}

/**
 * @param authority - B58 Encoded public key of the authority
 * @param actions.tokenAccount - B58 Encoded public key of the token account
 */
export interface ConstructionStateSerialized {
  construction: {
    authority: string;
    actions: (ActionMetadata & { tokenAccount: string })[];
    actionCalls: { callAmount: string; numbTimesHit: number }[];
    readyCallIdxList: number[];
  };
}

export interface BuildEphemeralOpts {
  preferredTokenAccounts?: {
    [mint: string]: PublicKey;
  };
}

export type ConstructionInstr = {
  actions: Action[];
  actionMap: BuildActionMap;
  initialSplits: number[];
  initialActionIndices: number[];
  amount: BNIsh;
  authority?: Signer;
  amountInAccount: PublicKey;
  amountInAuthority?: Signer;
  opts?: BuildEphemeralOpts;
};

export class MallocSdk {
  private readonly mallocProgram: Program;

  constructor(
    private readonly MALLOC_PROGRAM_ID: PublicKey,
    private readonly provider: Provider
  ) {
    this.mallocProgram = new Program(
      MALLOC_IDL as any,
      MALLOC_PROGRAM_ID,
      provider
    );
  }

  public async compileConstruction(
    inst: ConstructionInstr
  ): Promise<ConstructionCompiled> {
    return await this._compileConstructions(inst);
  }

  // TODO: prepare and run should j be one thing
  // Then, to deal with fact that token accounts can j be created, token creation tx's should j happen immediately preceding the first action to use that token

  // TODO create token accounts and store there associations
  // Append on "withdraw" functionality and tx to transfer initial into account
  private async _compileConstructions({
    actions,
    initialSplits,
    initialActionIndices,
    amount,
    authority: _authority,
    amountInAccount,
    actionMap,
    amountInAuthority: _inAuth,
    opts,
  }: ConstructionInstr): Promise<ConstructionCompiled> {
    let amountInAuthority = _inAuth || (this.provider.wallet as Wallet);
    let authority = _authority || (this.provider.wallet as Wallet);

    const { accounts: tokenAccounts, txs: tokenTxs } =
      // TODO: disable this and then later have an option to reenable
      await createTokenAccountsForActions(
        this.provider,
        authority,
        actions,
        this.provider.wallet as Wallet,
        opts?.preferredTokenAccounts
      );
    const { construction, tx: constructionTx } =
      await createAndInitConstruction(
        this.mallocProgram,
        authority.publicKey,
        actions.length
      );
    const addConstructionActionsTx = addActionsToConstruction(
      this.mallocProgram,
      construction.publicKey,
      actions.map((a) => a.actionMetadata),
      authority
    );

    if (!checkMintsAreTheSame(initialActionIndices.map((i) => actions[i]))) {
      throw errors.newCompilerError(
        "Expected all initial token types to be the same"
      );
    }

    if (initialActionIndices.length === 0)
      throw errors.newCompilerError(
        "Expected initial actions to have length more than 0"
      );

    const initMint = actions[initialActionIndices[0]].tokenMintIn;

    const transferTx = await transferToTx(
      amountInAccount,
      tokenAccounts[initMint.toBase58()].publicKey,
      new BN(amount), // Add 1 to ensure the accounts don't go to 0 // TODO: does this make sense
      amountInAuthority
    );

    const prepareConstructionTx = prepareConstruction(
      this.mallocProgram,
      construction.publicKey,
      initialActionIndices,
      initialSplits,
      authority,
      new BN(amount)
    );

    const hitOrder = buildSeqListOfActionCalls(
      actions.map((a) => a.actionMetadata),
      initialActionIndices
    );
    if (hitOrder.length !== actions.length)
      throw errors.newCompilerError(
        "An error occurred trying to forecast your construction"
      );

    const initResultAccounts = await Promise.all(
      actions.map((a) =>
        initResultAccount(
          new Program(
            ACTION_TEMPLATE_IDL as any,
            a.actionProgram,
            this.provider
          ),
          // Set the number of out amounts to equal the amount of next nodes
          a.actionMetadata.nextNodes.length
        )
      )
    );

    // The additional accounts added onto action action accounts follow conventional malloc standards for token
    // TODO: have an option to not pass in the token accounts
    const actionCtxFilled: PartialNextActionCtx[] = actions.map((action, i) => {
      const toAdd = action.addDefaultTokAccounts
        ? [
            {
              address: tokenAccounts[action.tokenMintIn.toBase58()].publicKey,
              isWriteable: true,
              isSigner: false,
            },
            {
              address: TOKEN_PROGRAM_ID,
              isWriteable: false,
              isSigner: false,
            },
            {
              address: authority.publicKey,
              isSigner: true,
              isWriteable: false,
            },
          ]
        : [];

      return {
        ctx: {
          resultAccount: initResultAccounts[i].resultAccount.publicKey,
          actionProgram: action.actionProgram,
        },
        actionAccounts: [...toAdd, ...action.actionAccounts] as AccountsArray,
      };
    });

    const processTxs = hitOrder.map((hitIndex) =>
      processAction(
        this.mallocProgram,
        hitIndex,
        construction.publicKey,
        actionCtxFilled[hitIndex],
        authority,
        {
          data: actions[hitIndex].actionData,
          instructions: actions[hitIndex].instructions,
          additionalSigners: actions[hitIndex].additionalSigners,
          preActionTxs: actions[hitIndex].preActionTxs,
        }
      )
    );

    const actionPIDToUID = getActionPIDToUID(actions, actionMap);

    const atStartOfPrepareTxs = getAllAtStartOfPrepareTxs(actions);
    // TODO: optional withdraw to accounts

    const txsPrepare = [
      ...atStartOfPrepareTxs,
      ...tokenTxs,
      transferTx,
      constructionTx,
      addConstructionActionsTx,
      prepareConstructionTx,
      ...initResultAccounts.map((i) => i.tx),
    ];
    const txsActionsCalls = [...processTxs.flat()];

    return {
      construction,
      txsPrepare: txsPrepare,
      txsActionCalls: txsActionsCalls,
      tokenAccounts,
      resultAccounts: initResultAccounts.map((a) => a.resultAccount),
      actionMap,
      actionPIDToUID,
    };
  }

  async getConstructionState(
    data: ConstructionCompiled
  ): Promise<ConstructionStateSerialized> {
    const constructionState: any =
      (await this.mallocProgram.account.construction.fetch(
        data.construction.publicKey
      )) as any;
    const actionSerialize = (a: any) => {
      return {
        tokenAccount: new PublicKey(a.tokenAccount).toBase58(),
        ripeAmount: a.ripeAmount,
        nextNodes: a.nextNodes.map((o) =>
          o.map((nextNode) => {
            return {
              actionIdx: nextNode.actionIdx,
              fraction: new BN(nextNode.fraction).toString(),
            };
          })
        ),
      };
    };
    const actionCallSerialize = (a: any) => {
      return {
        numbTimesHit: a.numbTimesHit,
        callAmount: new BN(a.callAmount).toString(),
      };
    };
    return {
      construction: {
        authority: new PublicKey(constructionState.authority).toBase58(),
        actions: constructionState.actions.map(actionSerialize),
        actionCalls: constructionState.actionCalls.map(actionCallSerialize),
        readyCallIdxList: constructionState.readyCallIdxList,
      },
    };
  }

  // TODO:!
  async deleteEphemeralAccounts(constructionData: ConstructionCompiled) {
    const withdrawAllRentInstr = (account: Signer) => {};
    constructionData.construction;
  }

  async runConstruction(
    construction: ConstructionCompiled,
    options?: RunConstructionOpts
  ): Promise<string[]> {
    try {
      return await this._runConstruction(construction, options);
    } catch (e) {
      throw errors.newRunError(
        e,
        construction.actionMap,
        construction.actionPIDToUID
      );
    }
  }

  private async _runConstruction(
    data: ConstructionCompiled,
    options?: RunConstructionOpts
  ): Promise<string[]> {
    await this.provider;
    if (options.splitPrepareAndActions) {
      const txHashesPrepare =
        data.txsPrepare.length > 0
          ? await this.provider.sendAll(data.txsPrepare, {
              // skipPreflight: true,
              ...(options?.splitPrepareAndActions.prepareCommitment || {}),
            })
          : [];
      if (
        options?.splitPrepareAndActions.onPrepareAccountsDone &&
        txHashesPrepare.length > 0
      )
        await options.splitPrepareAndActions.onPrepareAccountsDone(
          txHashesPrepare
        );
      // TODO: error handling for when preflight is skipped
      const txHashes = await this.provider.sendAll(data.txsActionCalls, {
        // skipPreflight: true,
        preflightCommitment: "single",
        commitment: "single",
        ...(options?.commitment || {}),
      });
      return [...txHashesPrepare, ...txHashes];
    } else {
      return await this.provider.sendAll([
        ...data.txsPrepare,
        ...data.txsActionCalls,
      ]);
    }
  }
}

const getActionPIDToUID = (
  actions: Action[],
  actionMap: BuildActionMap
): ActionPIDToActionUID => {
  const ret: ActionPIDToActionUID = {};
  actions.forEach((action) => {
    ret[action.actionProgram.toBase58()] = action.actionTypeUID;
  });
  return ret;
};

const createAndInitConstruction = async (
  program: Program,
  authority: PublicKey,
  numbActions: number
): Promise<{ construction: Signer; tx: SendTxRequest }> => {
  const ConstructionAccount = web3.Keypair.generate();
  const tx = program.transaction.initConstruction(numbActions, {
    accounts: {
      construction: ConstructionAccount.publicKey,
      authority,
      rent: web3.SYSVAR_RENT_PUBKEY,
    },
    signers: [ConstructionAccount],
    instructions: [
      await SystemProgram.createAccount({
        fromPubkey: program.provider.wallet.publicKey,
        newAccountPubkey: ConstructionAccount.publicKey,
        space: CONSTRUCTION_ACCOUNT_SIZE + 8,
        lamports: calculateRent(
          CONSTRUCTION_ACCOUNT_SIZE + 8,
          LIFETIME_CONSTRUCTION_ACCOUNT_MS
        ),
        programId: program.programId,
      }),
      // await program.account.construction.createInstruction,
    ],
  });

  return {
    tx: { tx, signers: [ConstructionAccount] },
    construction: ConstructionAccount,
  };
};

const addActionsToConstruction = (
  program: Program,
  construction: PublicKey,
  actions: ActionMetadata[],
  authority: Signer | Wallet
): SendTxRequest => {
  const signers = (authority as Signer).secretKey ? [authority as Signer] : [];
  const actionMetadataWithU64: ActionMetadata[] = actions.map((action) => {
    return {
      ripeAmount: action.ripeAmount,
      nextNodes: action.nextNodes.map((nextNode) =>
        nextNode.map((n) => {
          return {
            actionIdx: n.actionIdx,
            fraction: new u64(n.fraction),
          };
        })
      ),
    };
  });
  return {
    tx: program.transaction.addActions(actionMetadataWithU64, {
      accounts: {
        authority: authority.publicKey,
        construction: construction,
        rent: web3.SYSVAR_RENT_PUBKEY,
      },
      signers: signers,
    }),
    signers,
  };
};

const getAllAtStartOfPrepareTxs = (actions: Action[]) =>
  actions.map((a) => a.atStartOfPrepareTxs || []).flat();

const prepareConstruction = (
  program: Program,
  construction: PublicKey,
  initActionIdx: number[],
  initActionSplits: number[],
  authority: Signer | Wallet,
  amount: BN
): SendTxRequest => {
  const signers = (authority as Signer).secretKey ? [authority as Signer] : [];
  return {
    tx: program.transaction.prepareConstruction(
      initActionIdx,
      initActionSplits.map((i) => new BN(i)),
      amount,
      {
        accounts: {
          authority: authority.publicKey,
          construction: construction,
          rent: web3.SYSVAR_RENT_PUBKEY,
        },
        signers,
      }
    ),
    signers,
  };
};

/**
 * @param tokenAuthority - defaults to the authority if not provided
 */
const processAction = (
  program: Program,
  actionIdx: number,
  construction: PublicKey,
  actionAccountsCtx: PartialNextActionCtx,
  authority: Signer | Wallet,
  opts: {
    data?: Buffer;
    tokenAuthority?: Signer | Wallet;
    instructions?: TransactionInstruction[];
    additionalSigners?: Signer[];
    preActionTxs?: SendTxRequest[];
  }
): SendTxRequest[] => {
  const auth = (authority as Signer)?.secretKey ? [authority as Signer] : [];
  const tokauth = (opts.tokenAuthority as Signer)?.secretKey
    ? [opts.tokenAuthority as Signer]
    : [];
  const signersIsolated = opts.tokenAuthority
    ? [...tokauth, ...auth]
    : [...auth];
  const signers = [...signersIsolated, ...(opts.additionalSigners || [])];
  return [
    ...(opts.preActionTxs || []),
    {
      tx: program.transaction.processAction(
        actionIdx,
        opts.data || Buffer.from([]),
        {
          accounts: {
            construction: construction,
            rent: web3.SYSVAR_RENT_PUBKEY,
            authority: authority.publicKey,
            actionCtxProxy: actionAccountsCtx.ctx,
            actionAccounts: actionAccountsCtx.actionAccounts,
          } as ProcessActionNext,
          signers,
          instructions: opts.instructions || undefined,
        }
      ),
      signers,
    },
  ];
};

const initResultAccount = async (
  actionTemplateProgram: Program,
  numberOfPossibleOutputTokens: number
): Promise<{ tx: SendTxRequest; resultAccount: Signer }> => {
  const result = Keypair.generate();
  actionTemplateProgram.provider.connection.getMinimumBalanceForRentExemption;
  const resultSize = calcSizeResultAccount(numberOfPossibleOutputTokens);
  const tx = actionTemplateProgram.transaction.initResultAccount(
    numberOfPossibleOutputTokens,
    {
      accounts: {
        result: result.publicKey,
        rent: web3.SYSVAR_RENT_PUBKEY,
      },
      instructions: [
        await SystemProgram.createAccount({
          fromPubkey: actionTemplateProgram.provider.wallet.publicKey,
          newAccountPubkey: result.publicKey,
          space: resultSize,
          lamports: calculateRent(resultSize, LIFETIME_RESULT_ACCOUNT_MS),
          programId: actionTemplateProgram.programId,
        }),
      ],
      signers: [result],
    }
  );
  return {
    tx: { tx, signers: [result] },
    resultAccount: result,
  };
};

