// 8 for the discriminator, 32 for the owner pubkey, 4 for the number of results, and the rest for numb actions, then 64 as a buffer
const calcSizeResultAccount = (numberOfActions: number) =>
  8 + 32 + 4 + numberOfActions * 8 + 64;

const checkMintsAreTheSame = (actions: Action[]) => {
  if (actions.length === 0) return true;
  const mint = actions[0].tokenMintIn.toBase58();
  return actions.every((a) => a.tokenMintIn.toBase58() === mint);
};
