import React, { FC } from "react";
import Spreadsheet from "react-spreadsheet";

import {
  Input,
  ActionEdge,
  NodeType,
  GraphType,
  NextAction,
  InputClassNames,
  SourceNodeInput,
} from "src/interfaces";
import { useStore } from "react-stores";
import {
  globalStore,
  MallocState,
  setGlobalStoreFromFile,
  SOURCE_NODE_ID,
} from "../globalStore";
import { compileAndRunMallocSDK } from "src/utils/sdk-interpelator";
import { useAnchorWallet, useWallet } from "@solana/wallet-adapter-react";
import { getNextActionRow, getSourceNodeAccount } from "src/utils/graph";
import { MallocSPLBuildActionMap } from "@malloc/spl";
import { saveStringToFile } from "src/utils/files";
import { getMintInfo, getTokenAccountInfo } from "src/utils/spl-tokens";
import {
  setHardCodedNextActions,
} from "src/utils/action-types";
import { utils } from "@malloc/sdk";
import { getProvider } from "src/utils/anchor";
import { PublicKey } from "@solana/web3.js";
import { TableCellEditor } from "./table/table-cells";

const NodeInfo: FC<{}> = () => {
  const wallet = useAnchorWallet();
  const state = useStore(globalStore);


  const setInput = async (data: Input) => {
    state.graph[state.selected].input = data as Input;
    let mintName = state.sourceMint;
    if (state.selected === SOURCE_NODE_ID) {
      const sourceAccount = getSourceNodeAccount(data as SourceNodeInput);
      if (sourceAccount) {
        const info = await getTokenAccountInfo(
          getProvider(wallet!),
          new PublicKey(sourceAccount)
        );
        if (info) {
          mintName = info.mint.toBase58();
        }
      }
    }
    globalStore.setState({
      ...state,
      sourceMint: mintName,
      graph: { ...state.graph },
    });
  };

  if (!state.graph[state.selected])
    return <>{state.selected} is a non existent action</>;

  return (
    <div className="card">
      <div className="row">
        <div className="col-10">
          <h2>{state.selected} info</h2>
        </div>
        <div className="col-2">
          {state.selected != "SOURCE-0" && (
            <button
              onClick={() => {
                let deleted_key = state.selected;
                if (deleted_key == "SOURCE-0") return;
                const graph = utils.object.deepCloneObject(state.graph);
                delete graph[deleted_key];

                for (var key of Object.keys(graph)) {
                  let next_actions = graph[key]["next_actions"];
                  if (next_actions != null && next_actions.length != 0) {
                    for (let x = 0; x < next_actions.length; x++) {
                      // Start with 1 as the first row is the labels
                      for (var i = 1; i < next_actions[x].length; i++) {
                        let edge = next_actions[x][i][0].value;
                        if (edge == deleted_key) {
                          // LEV look here
                          console.log(next_actions[x][i][0]);
                          next_actions[x][i][0].value = "";
                        }
                      }
                    }
                  }
                  graph[key]["next_actions"] = next_actions;
                }

                globalStore.setState({
                  ...state,
                  selected: SOURCE_NODE_ID,
                  graph,
                });
              }}
            >
              X
            </button>
          )}
        </div>
      </div>

      {state.selected === SOURCE_NODE_ID && (
        <p>Source Mint: {state.sourceMint}</p>
      )}

      <h5>Input</h5>
      <Spreadsheet
        hideColumnIndicators={true}
        hideRowIndicators={true}
        data={state.graph[state.selected].input}
        onChange={(data) => setInput(data as Input)}
        DataEditor={TableCellEditor}
      />

      {state.graph[state.selected].next_actions.length != 0 && (
        <div>
          <br />
          <h5>Next Action</h5>
        </div>
      )}
      {state.graph[state.selected].next_actions.map(
        (next_action_by_mint, i) => {
          return (
            <div key={`next-action-${i}`}>
              <Spreadsheet
                hideColumnIndicators={true}
                hideRowIndicators={true}
                data={state.graph[state.selected].next_actions[i]}
                onChange={(_data) => {
                  const dataWithHardCoded = setHardCodedNextActions(
                    _data as NextAction[]
                  );
                  state.graph[state.selected].next_actions[i] =
                    dataWithHardCoded as NextAction[];
                  globalStore.setState({
                    ...state,
                    graph: { ...state.graph },
                  });
                  console.log(state.graph[state.selected].next_actions);
                }}
                DataEditor={TableCellEditor}
              />
              <br />
              <div className="row">
                <div className="col-2">
                  <button
                    onClick={() => {
                      state.graph[state.selected].next_actions[i] = [
                        ...state.graph[state.selected].next_actions[i],
                        getNextActionRow(i),
                      ];
                      globalStore.setState({
                        ...state,
                        graph: { ...state.graph },
                      });
                      console.log(
                        "new action",
                        state.graph[state.selected].next_actions
                      );
                    }}
                  >
                    Add
                  </button>
                </div>
                <div className="col-2">
                  <button
                    onClick={() => {
                      if (
                        state.graph[state.selected].next_actions[i].length > 2
                      ) {
                        let temp = state.graph[state.selected].next_actions[i];
                        state.graph[state.selected].next_actions[0] =
                          temp.slice(0, temp.length - 1);
                        globalStore.setState({
                          ...state,
                          graph: { ...state.graph },
                        });
                        console.log(
                          "remove action",
                          state.graph[state.selected].next_actions
                        );
                      }
                    }}
                  >
                    Remove
                  </button>
                </div>
                <div className="col"></div>
              </div>
            </div>
          );
        }
      )}
    </div>
  );
};

export default NodeInfo;
