import Input from "@material-ui/core/Input";
import { getVertexType } from "src/utils/graph";
import MenuItem from "@mui/material/MenuItem";
import Select from "@mui/material/Select";
import FocusTrap from "focus-trap-react";
import { useCallback, useEffect, useState } from "react";
import {
  CellBase,
  DataEditorComponent,
  DataEditorProps,
} from "react-spreadsheet";
import { useStore } from "react-stores";
import { globalStore, MallocState } from "src/globalStore";
import { InputClassNames } from "src/interfaces";
import { getActionVertices } from "src/utils/graph";
import TokenAccountChooser from "../tokens/TokenAccountChooser";

const InputEditor = (
  props: DataEditorProps<CellBase> & {
    isWholeNumber?: boolean;
    isFractionNumber?: boolean;
  }
) => {
  return (
    <Input
      onChange={(e) => {
        props.onChange({
          ...props.cell,
          value: props.isWholeNumber
            ? parseInt(e.target.value).toString()
            : e.target.value,
        });
      }}
      value={props.cell?.value || ""}
      type={props.isFractionNumber || props.isWholeNumber ? "number" : "text"}
    />
  );
};

const TokenOrMintEditor = (
  props: DataEditorProps<CellBase> & {
    type: "mint" | "token";
    onlyWithInitialized: boolean;
  }
) => {
  return (
    <div>
      <TokenAccountChooser
        buttonText={"select"}
        onlyWithInitializedAccounts={props.onlyWithInitialized}
        onClose={(splTok) => {
          if (splTok) {
            const value =
              props.type === "token"
                ? splTok.account
                : splTok.info.mint.toBase58();
            props.onChange({
              ...props.cell,
              value,
            });
          }
        }}
      />
    </div>
  );
};

const ActionVertexEditor = (
  props: DataEditorProps<CellBase> & {
    nextActionMintIdx: number;
  }
) => {
  const store = useStore(globalStore);
  const vertices = getActionVertices(store.graph);
  const [active, setActive] = useState(true);
  const [pause, setPause] = useState(false);

  const setState = (id: string) => {
    props.onChange({ ...props.cell, value: id });
    setActive(false);
  };

  return (
    <div>
      <FocusTrap active={active} paused={pause}>
        <Select
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          value={props.cell?.value}
          label="Age"
          style={{
            height: "20px",
          }}
        >
          {vertices.map((id) => {
            return (
              <span key={`select-opts-${id}`}>
                <MenuItem
                  style={{ zIndex: 100 }}
                  value={id}
                  onMouseOver={() => setPause(true)}
                  onFocus={() => setState(id)}
                >
                  {id}
                </MenuItem>
              </span>
            );
          })}
        </Select>
      </FocusTrap>
    </div>
  );
};

export const TableCellEditor: DataEditorComponent<CellBase> = (
  props: DataEditorProps<CellBase>
) => {
  const className = (props.cell?.className || "").split("-")[0];
  switch (className) {
    case InputClassNames.actionVertex:
      const nextActionMintIdx = parseInt(
        props.cell!.className!.split("-")[1] || ""
      );
      return ActionVertexEditor({ ...props, nextActionMintIdx });
    case InputClassNames.mintAccountOnlyInit:
      return TokenOrMintEditor({
        ...props,
        type: "mint",
        onlyWithInitialized: true,
      });
    case InputClassNames.tokenAccountOnlyInit:
      return TokenOrMintEditor({
        ...props,
        type: "token",
        onlyWithInitialized: true,
      });
    case InputClassNames.mintAccount:
      return TokenOrMintEditor({
        ...props,
        type: "mint",
        onlyWithInitialized: false,
      });
    case InputClassNames.tokenAccount:
      return TokenOrMintEditor({
        ...props,
        type: "token",
        onlyWithInitialized: false,
      });
    case InputClassNames.other:
      return InputEditor(props);
    case InputClassNames.wholeNumber:
      return InputEditor({ ...props, isWholeNumber: true });
    case InputClassNames.fractionNumber:
      return InputEditor({ ...props, isFractionNumber: true });
    default:
      return InputEditor(props);
  }
};
