import {
  Account,
  Connection,
  Keypair,
  PublicKey,
  Signer,
  Transaction,
  TransactionInstruction,
} from "@solana/web3.js";
import { MintInfo, TOKEN_PROGRAM_ID, u64 } from "@solana/spl-token";
import {
  ActionLib,
  AccountsArray,
  Action,
  ActionMetadata,
  BuildIsolatedActionFn,
  BuildIsolatedActionFnOpts,
  utils,
} from "@malloc/sdk";
import { BN, Provider, web3 } from "@project-serum/anchor";
import { TokenInfo, TokenListContainer } from "@solana/spl-token-registry";
import {
  connection,
  createMintInstructions,
  createTokenAccountInstrs,
  parseMintAccount,
  SendTxRequest,
} from "@project-serum/common";
import IDL from "../../target/idl/mint_action.json";
import { Idl } from "../../../../anchor/ts/src";

const actionTypeUID = "MALLOC_SPL_MINT";

type BuildIsolatedMintArgs = {
  authority?: string;
  decimals: string;
  amountOut: string;
};

export const buildIsolatedMintAction: BuildIsolatedActionFn<BuildIsolatedMintArgs> =
  async (
    inp: BuildIsolatedMintArgs,
    inMint: string,
    inTokenAccount: string,
    dummyPassThroughActionPID: string,
    provider: Provider,
    opts?: BuildIsolatedActionFnOpts<BuildIsolatedMintArgs>
  ) => {
    const decimals = parseInt(inp.decimals);
    const mint = new Keypair();
    const amountOutExpanded = utils.tokens.fromReadableNumber(
      parseFloat(inp.amountOut),
      decimals
    );

    const outTokenAccount = await utils.tokens.getTokenAccountUsedByMalloc(
      provider.wallet.publicKey,
      mint.publicKey,
      opts?.preferredTokenAccounts
    );

    const authority = new PublicKey(inp.authority || provider.wallet.publicKey);

    return {
      isolatedAction: {
        addDefaultTokAccounts: false,
        actionData: serializeData(amountOutExpanded.toString()),
        tokenAccountOuts: [outTokenAccount],
        actionAccounts: [],
        actionProgram: new PublicKey(dummyPassThroughActionPID),
        tokenMintIn: new PublicKey(inMint),
        tokenMintOuts: [mint.publicKey],
        additionalSigners: [mint],
        tokenCreationOptions: {
          skipOutTokenCreations: [0],
        },
        instructions: [
          ...(await createMintInstructions(
            provider,
            authority,
            mint.publicKey,
            decimals
          )),
        ],
      },
    };
  };

const serializeData = (amount: string) => Buffer.from(`[${amount}]`);

/**
 * The file's main export
 */
export const MintAction: ActionLib<BuildIsolatedMintArgs> = {
  buildIsolatedAction: buildIsolatedMintAction,
  actionTypeUID,
  expectedNumberOfOutMints: 1,
  idl: IDL as Idl,
};
export default MintAction;
