import { ActionPIDToActionUID, BuildActionMap, utils } from "..";
import {
  MallocActionBuilderError,
  MallocBuilderError,
  MallocCompilerError,
  MallocRunnerError,
} from "../interfaces/errors";

export const newActionBuilderError = (
  actionTypeUID: string,
  actionIdx: number,
  message: string
): MallocActionBuilderError => {
  return {
    mallocErrorMarker: "MALLOC ERROR MARKER",
    type: "Malloc Action Builder",
    message,
    actionIndex: actionIdx,
    actionTypeUID,
  };
};

export const newBuilderError = (message: string): MallocBuilderError => {
  return {
    mallocErrorMarker: "MALLOC ERROR MARKER",
    type: "Malloc Builder",
    message,
  };
};
export const newCompilerError = (message: string): MallocCompilerError => {
  return {
    mallocErrorMarker: "MALLOC ERROR MARKER",
    type: "Malloc Compiler",
    message,
  };
};

export const newRunError = (
  e: any,
  actionMap: BuildActionMap,
  actionPIDToUID: ActionPIDToActionUID
): MallocRunnerError => {
  let message = "";
  console.log(JSON.stringify(e));

  if (e.logs) {
    const logs: string[] = e.logs;
    // const errorLogs = utils.object.findAllIndexes(logs, (log: string) =>
    //   log.toLocaleLowerCase().includes("error")
    // );
    // console.log(errorLogs);
    let customErrorHit = false;
    for (let i = 0; i < logs.length; i++) {
      const { msg, customErrorHit: _c } = logLineToMessageLine(
        logs,
        i,
        actionMap,
        actionPIDToUID,
        customErrorHit
      );
      customErrorHit = _c;
      message += `${msg}`;
    }
  }
  console.log(message);
  return {
    mallocErrorMarker: "MALLOC ERROR MARKER",
    type: "Malloc Runner",
    message: message,
  };
};

const logLineToMessageLine = (
  logLines: string[],
  idx: number,
  actionMap: BuildActionMap,
  actionPIDToUID: ActionPIDToActionUID,
  customErrorHit: boolean
): { msg: string; customErrorHit: boolean } => {
  const defaultMsg = () => {
    return { msg: logLines[idx] + "\n", customErrorHit };
  };
  if (
    logLines[idx].includes("Custom program error: ") &&
    logLines.length > idx + 1 &&
    logLines[idx + 1].includes("Program")
  ) {
    if (customErrorHit) return { msg: "", customErrorHit: true };
    const errorNumber = Number(
      logLines[idx].split("Custom program error: ")[1],
      // @ts-ignore
      10
    );
    const PID = logLines[idx + 1].split(" ")[1];
    const uid = actionPIDToUID[PID];
    if (!uid) return defaultMsg();
    if (!actionMap[uid]?.idl) return defaultMsg();
    const errMsg = actionMap[uid].idl.errors.find(
      (e) => e.code === errorNumber
    );
    if (!errMsg) return defaultMsg();
    return {
      msg: `${uid} runtime error: ${errMsg.name} - ${errMsg.msg}`,
      customErrorHit: true,
    };
  } else if (logLines[idx].includes("error")) {
    return defaultMsg();
  } else {
    return { msg: "", customErrorHit };
  }
};
